$canvas-banner-padddings: 40px !default;

.canvas-banner {
	margin-bottom: 50px;
	text-align: center;

	.banner-body {
		position: relative;
		background: white;
		display: block;
		margin: 0 auto;
		border: 1px solid rgba(0, 0, 0, 0.3);
		border-radius: $border-radii;
		overflow: hidden;
		box-sizing: border-box;

		@include screen-size(medium-large) {
			max-width: 80%;
		}

		.banner-text {
			display: block;

			@include screen-size(medium-large) {
				padding: $canvas-banner-padddings $canvas-banner-padddings 0 calc(30% + #{$canvas-banner-padddings});
			}

			@include screen-size(small) {
				padding: 15px;

				.button-bar {
					margin: 0 0 -15px 0;
				}
			}

			p {
				margin-bottom: 20px;

				a {
					text-decoration: underline;
				}

				&.button-bar {
					margin: 0;

					.pat-button {
						margin-bottom: $canvas-banner-padddings;
						text-decoration: none;
					}
				}
			}


			.banner-counter {
				float: right;
				margin-left: 15px;
				font-weight: bold;
				color: rgba(0, 0, 0, 0.5);
			}
		}

		img {
			@include screen-size(medium-large) {
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				object-fit: cover;
				width: 30%;
				height: 100%;
			}

			@include screen-size(small) {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 60px;
			}
		}
	}
}