@charset "UTF-8";

$colour-timeline: $colour-accent;

.object-history {
  position: relative;
  
  &:before {
    content: ' ';
    display: block;
    position: absolute;
    width: 0.3em;
    top: 2em;
    bottom: 2em;
    left: 1.75em;
    background: $colour-timeline;
    z-index: 1;
  }

  .object {
    margin-bottom: 1em;
    padding-left: 4.2em;
    position: relative;
    @include clear();

    .meta {
        &[href]:hover {
          .user-name {
            text-decoration: underline;

            &:after {
              font-family: fontello;
              content: ' #{$glyph-mail}';
              display: inline-block;

              position: absolute;
              padding-left: 10px;
              margin-top: -2px;
            }
          }
        }
    }

    .user-name {
      padding-top: 0.55em;
    }

    &.user-inactive {
      .meta {
        color: gray;

        .pat-avatar {
          filter: grayscale(100%) brightness(200%) contrast(40%);
        }
      }
    }

    .byline {
      padding-bottom: 0.4em;
      margin-bottom: 0;

      a {
          text-decoration: underline;
          
          &:hover {

          }
      }

      * {
        font-style: normal;
      }
    }

    .message {
      margin: 0 0 7px 0;
      font-size: 16px;
      line-height: 1.4;
      color: rgba(0,0,0,0.5);
    }

    &.mutation-sampled {
      .sample-subject {
        // display: block;
        font-style: normal;
        font-weight: bold;

        &:after {
          font-family: fontello;
          display: inline-block;
          margin-left: 0.3em;
          font-style: normal;
          font-weight: normal;
        }
      }

      // &.sample-passed .sample-date {
      //   color: green;
      // }

      // &.sample-problem .sample-date {
      //   color: red;
      // }

      &.sample-passed .sample-subject {
        color: green;

        &:after {
          content: "#{$glyph-ok-circled}";
        }
      }

      &.sample-problem .sample-subject {
        color: red;
        
        &:after {
          content: "#{$glyph-cancel-circled}";

        }
      }
    }

    .pat-avatar {
      position: absolute;
      left: 0;
      top: 0;
      border: $colour-timeline 0.3em solid;
      width: 3.2em;
      height: 3.2em;
      z-index: 1;
    }
  }

  &.action-centric {
    // @include screen-size(large) {
    //   margin-left: -3.2em;
    // }

    &:before {
      display: none;
    }

    .object {
      background-color: rgba(0,0,0,0.08);
      margin-bottom: 0.25em;
      // @include screen-size(large) {
      //   border-bottom-right-radius: 2.3em;
      //   border-top-right-radius: 2.3em;
      // }
      padding-left: 3.2em;
      padding-right: 1em;

      .title {

        &:before {
          font-family: fontello;
          content: '';
          display: block;
          margin: 0;
          position: absolute;
          left: 0.96em;
          top: 0.96em;
          // background: white;
          color: rgba(0,0,0,0.5);
          line-height: 1.9em;
          width: 1.6em;
          height: 1.85em;
          font-size: 1em;
          z-index: 1;
          text-align: center;
        }

        &.action-edited:before {
          content: '\270e';
        }

        &.action-deleted:before {
          content: '\e729';
        }

        &.action-created:before {
          content: '\e831';
          line-height: 2em;
        }

        &.action-state:before {
          content: '\e852';
        }
      } 
    }
  }
}