@charset "UTF-8";

$portlet-padding: 30px !default;

@mixin title-link {
	text-decoration: none;
	color: black;

	&[href] {
		&:not(.follow):after {
			font-family: fontello;
			content: '#{$glyph-link-internal}';
			margin-left: 0.5em;
			font-size: 0.8em;
			text-decoration: none !important;
			position: absolute;
			font-weight: normal;
			line-height: 1.7;
		}

		&:hover {
			text-decoration: underline;

			&:after {
				text-decoration: none !important;
			}

			&:before {
				text-decoration: none !important;
			}
		}
	}
}

.portlet {
	&.height-full {
		@include screen-size(medium-large) {
			min-height: 100%;
		}

		// .content {
		// 	height: 100%;
		// }
	}

	@include screen-size(small) {
		width: calc(100% + 2 * #{$small-screen-padding});
    	margin-left: -1 * $small-screen-padding;
    	margin-right: -1 * $small-screen-padding;
	}

	&.finger-tabs {
		.portlet-body {

			@include screen-size(medium-large) {
				margin: -6.8%;
				display: flex;
				padding: 0;
				overflow: hidden;
			}

			.finger-tabs-sidebar {
				box-sizing: border-box;
				background: linear-gradient(90deg, rgba(0,0,0,0) 90%, rgba(0,0,0,0.03) 100%);

				@include screen-size(medium-large) {
					width: 33%;	
					padding: 25px 0 100px 25px;
				}

				@include screen-size(small) {
					margin-bottom: 20px;
				}

				.finger-tabs-sidebar-buttons {
					@include screen-size(medium-large) {
						position: absolute;
						left: 25px;
						bottom: 25px;
					}

					@include screen-size(small) {
						float: right;
					}

					.button {
						@include pat-button;
						margin-bottom: 0;

						@include screen-size(small) {
							margin-bottom: 10px;
						}

						&.icon {
							display: inline-block;
							width: $button-height;
							height: $button-height;

							&.add-organisation {
								&:before {
									content: $glyph-plus;
								}
							}

							&:before {
								display: block;
								line-height: $button-height;
								font-family: fontello;
								text-align: center;
							}
						}
					}
				}

				.finger-tabs-tabs {

					.finger-tabs-tab {
						@include screen-size(small) {
							display: inline-block;
							margin-right: 10px;
						}

						a {
							@include screen-size(small) {
								margin-bottom: 10px;
								display: inline-block;
								line-height: $button-line-height;
								padding: 0 20px;
								border-radius: 50px;
								background: $button-background-colour;
								color: $button-text-colour;

								&.current {
									background: $colour-accent;
									color: white;
								}
							}

							@include screen-size(medium-large) {
								padding: 25px;
								display: block;
								border-top-left-radius: $border-radii;
								border-bottom-left-radius: $border-radii;
								font-weight: bold;
								color: black;

								&.current {
									background: white;
									box-shadow: 0 0 10px rgba(black, 0.1);
									cursor: default;
								}

								&:not(.current) {
									&:hover {
										text-decoration: underline;
									}
								}
							}
						}
					}
				}
			}

			.finger-tabs-sheets {
				display: block;
				border-top-right-radius: $border-radii;
				border-bottom-right-radius: $border-radii;
				position: relative;

				.finger-tabs-sheet-buttons {
					.pat-button {
						margin-bottom: 10px;

						&.float-after {
							float: right;
							margin-right: 0;
							margin-left: 10px;
						}
					}
				}

				@include screen-size(medium-large) {
					width: 67%;
					background: white;

					.finger-tabs-sheet {
						padding: 6% 6% 100px;

						.finger-tabs-sheet-buttons {
							position: absolute;
							left: 0;
							right: 0;
							bottom: 25px;
							padding: 0 6%;
						}
					}
				}
			}
		}
	}

	&#portlet-my-risk-assessments {
		.separation-title {
			margin-bottom: 8px;
			margin-top: 30px;
		}

		.pat-table {
			.item-name {
				@include screen-size(medium-large) {
					width: 50%;
				}
			}
		}

		.list-footer {
			font-size: 0.8em;
			margin: 15px 0 0 0;
			padding-left: 8px;

			a {
				text-decoration: none;
				font-weight: 400;

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	&#portlet-company {
		.content {
			// background: white;

			.portlet-logo {
				margin: 0;
			}

			.button-bar {
				// margin-top: 20px;
			}

			.portlet-body {
				// padding-top: 34px;
			}

			.organisation-logo {
				text-align: center;
				display: block;
				margin-top: 10px;
				margin-bottom: 30px;

				img {
					display: inline-block;
					max-height: 70px;
				}
			}
		}
	}

	&#portlet-unlocked-assessments {
		.content {
			.pat-table {
				.item-name {
					width: auto;
				}
			}

			.portlet-footer {
				margin: 15px 0 0 0;
			}
		}
	}

	&#portlet-accept-membership {
		.content {
			background: white;

			.portlet-logo {
				margin: 0 0 40px;
				text-align: center;

				img {
					max-width: 340px;
				}
			}

			.button-bar {
				margin-top: 40px;
			}
		}
	}



	.button-bar:last-child {
		margin-bottom: -15px;
	}

	.content {
		position: relative;
		box-shadow: $default-box-shadow;
		background-color: $colour-portlet-background;
		padding: 6%;

		@include screen-size(medium-large) {
			border-radius: $border-radii;
		}

		.context-menu .panel-content {
			width: 400px;
		}

	    .portlet-intro {
			display: flex;

			.portlet-tool-group {
				// flex: 1;
				font-size: 16px;

				> .pat-select,
				.context-menu {
					float: right;
				}
			}

			.portlet-title-group {
				flex: 4;

				h1 {
					margin-bottom: 30px;
					font-size: bold;
				}

				.byline {
					margin-top: -30px;
					margin-bottom: 30px;
				}
			}

		}

		.portlet-body {

			.portlet-toolbar {
				margin-bottom: 1em;

				@include clear;

				.pat-select {
					float: left;
				}
			}

			h3 {
				font-size: 1em;
			}

			.separation-title {
				font-size: 1em;
				margin: 0 0 16px 0;
				font-weight: bold;
				
				a {
					@include title-link;
				}
			}

			.training {
				padding-top: 1em;
				
				.auto-scale-wrapper {
					overflow: hidden;
				}

				.training-title {
					margin-bottom: 0.5em;
				}

				.certificate-link {
					margin-bottom: 1em;
					float: left;

					* {
						cursor: pointer;
					}
				}
			}

			.certificates {
				.auto-scale-wrapper {
					overflow: hidden;
					margin-bottom: 1em;
				}

				a.certificate-link {
					float: left;

					* {
						cursor: pointer;
					}
				}
			}
		}

	    @include screen-size(small) {
	    	// padding: 0;
	    	padding: 30px $small-screen-padding;
	    	border: none;
	    	// background-color: transparent;
	    }
	}

	margin-bottom: 2rem;

	.portlet-logo {
		text-align: center;
		margin-bottom: 1.4em;

		img {
			@include screen-size(small) {
				max-width: 90%;
			}
			@include screen-size(medium-large) {
				max-width: 70%;
			}
		}
	}

	.portlet-title {
		// font-weight: 300;
		font-stretch: condensed;
		margin: 0 0 1rem 0;
		font-size: 20px;
		font-weight: bold;

		@include screen-size(medium) {
			font-size: 1;
		}

		a:hover {
			text-decoration: underline;
		}

		a:before {
			font-family: fontello;
			font-size: 0.7em; 
			margin-right: 0.8em;
			display: inline-block;
			vertical-align: middle;
			margin-top: -0.4em;
			width: 2em;
			height: 2em;
			text-align: center;
			line-height: 2.1em;
			border-radius: 2px;
			background-color: $colour-accent;
			color: white;
		}

		sup {
			font-size: 0.6em;
			color: gray;
		}
	}

	.link {
		// padding: 0.2em 0;
		
		&:hover {
			text-decoration: none;
		}
	}

	.portlet-footer {
		font-size: 0.8em;
		
		.more-link {
			font-weight: 400;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	.title {
		margin: 0;
		font-size: 1em;
		font-weight: normal;
	}

	.byline {
		color: #666;
		font-size: 0.8em;
		line-height: 1.3em;
		font-style: italic;
		margin: 0;
	}

	table {
		td {
			&.date-finished {
				width: 20%;
			}
		}
	}

	.pat-rich {
		table {
			a {
				text-decoration: none;

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

}
