$button-spacing: 0.8em !default;

$nav-bar-button-height-mobile: 35px !default;
$nav-bar-button-line-height-mobile: 36px !default;

$nav-bar-button-height: 49px !default;
$nav-bar-button-line-height: 49px !default;

.button-bar {
	margin-bottom: 1em;
	overflow-y: hidden;

	.button-set {
		&.float-after {
			margin-left: 15px;
			float: right;

			.pat-button {
				margin-right: 0;
				margin-left: $button-spacing;
			}
		}
	}

	.pat-button {
		vertical-align: middle;
		margin-right: $button-spacing;
		margin-bottom: $button-spacing;
		float: left;
		vertical-align: baseline;
		
		&.float-after {
			float: right;
			margin-right: 0;
			margin-left: $button-spacing;
		}
	}

	.iconified:not(.pat-button) {
		float: right;
		line-height: 1.5em;
		border-left: 1em solid transparent;
		border-right: 0 solid transparent;
		margin: -0.5em 0;
		padding: 0.5em 0;
		color: #666;
  		cursor: default;
  		
  		&:hover,
  		&:active {
  			color: black;
  		}
	}

	&#nav-bar {

		& + * {
			margin-top: 30px;
		}

		#main-content > &,
		#main-content > form > & {
			.pat-button.continue,
			.pat-button.skip,
			.pat-button.float-after {
				float: right;
				margin-right: 0;
				margin-left: $button-spacing;

			}
		}

		.pat-button.skip,
		.pat-button.back,
		.pat-button.back:before {
			// background: $button-background-colour;
			color: $button-text-colour;
		}

		.pat-button.back:hover:before,
		.pat-button.skip:hover {
			color: $button-text-colour-hover;
		}
		
		@include screen-size(small) {
			position: fixed;
			bottom: 0;
			margin: 0;
			left: 10px;
			right: 10px;
			background: $colour-base;
			padding: 15px 0 0.5em;
			@include clear;
			z-index: 10;
			border-top: 1px solid black;
			transition: transform 1s;
			font-size: 120%;

			.focus-sidebar & {
				transform: translateY(100%);
			}

			.pat-button {
				padding: 0 1em;
				font-size: 0.7em;
				height: $nav-bar-button-height-mobile;
				line-height: $nav-bar-button-line-height-mobile;
				position: relative;
				max-width: 100%;
				margin-bottom: 0.5em;
			}
		}
		@include screen-size(medium-large) {
			position: absolute !important;
			top: auto !important;
			bottom: 0 !important;
			margin: 0 1rem; // Margin left and right required to not cover scrollbar in IE.
			left: 0;
			right: 0;
			z-index: 10;
			padding: 1em 1rem calc(1em - #{$button-spacing});
			// background-color: white;
			background: $colour-base;

			&.bumped-bottom:after,
			&.bumped-top:after {
				position: absolute;
				right: 15px;
				left: 15px;
				top: 0;
				height: 1px;
				content: ' ';
				display: block;
				z-index: -1;
				pointer-events: none;
				background-color: $colour-separation-lines;
			}

			#content-pane > & {
				position: sticky !important;
				margin: 0;
				padding: 1em 0 calc(1em - #{$button-spacing});

				&.bumped {
					&:after {
						display: block;
					}
				}

				&:after {
					left: 0;
					right: 0;
					display: none;
				}
			}
		}

		.pat-button {

			@include screen-size(medium-large) {
				height: $nav-bar-button-height;
				line-height: $nav-bar-button-line-height;
				padding: 0 45px;
			}

			&.back {
				width: 35px;
				color: transparent;
				text-align: center;
				overflow: hidden;
				padding: 0;
				position: relative;

				&:before {
					font-family: fontello;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					content: '#{$glyph-angle-left}';
					font-size: 28px;
					@include screen-size(medium-large) {
						line-height: 48px;
					}
				}

				&[disabled],
				&.disabled {
					background-color: $button-background-colour-disabled;
					box-shadow: none;

					&:before {
						color: $button-text-colour-disabled;
					}
				}
			}

			&.continue {
				background-color: $button-background-colour-default;
				color: $colour-button-continue-text;
				box-shadow: $button-shadow-default;

				&:hover {
					background-color: $button-background-colour-default-hover;
				}

				&[disabled],
				&.disabled {
					background-color: $button-background-colour-disabled;
					color: $button-text-colour-disabled;
				}
			}
		}
	}
}

.pat-checklist.pat-clone + .button-bar.cloning {
	transition: padding-left 0.4s;
	margin-top: -0.8em;
}

.pat-checklist.pat-clone.has-value + .button-bar.cloning {
	padding-left: 1.5em;
	margin-top: -0.8em;
}