.assessment-toc {
	display: table;

	.item {
		display: table-row;

		&.current {
			.field {
				.index-number {
					background: $colour-accent;
					color: white;
					transform: scale(1.2);
				}
			}
		}

		&:hover {
			.label {
				text-decoration: underline;
			}
		}

		&.module {
			&:not(:first-child) {
				.field {
					padding-top: 15px;
				}
			}

			.label {
				font-weight: bold;
				color: black;
			}
		}

		.field {
			display: table-cell;
			padding-left: 15px;
			padding-top: 3px;
			padding-bottom: 3px;

			&:first-child {
				padding-left: 0;
			}

			.index-number {
				display: inline-block;
				background: rgba(black, 0.1);
				padding: 1px 8px;
				vertical-align: middle;
				font-size: 12px;
				border-radius: 0.7 * $border-radii;
				color: rgba(black, 0.55);
				font-family: monospace;		
				font-weight: bold;		
				letter-spacing: -1.5px;
				position: relative;
				top: -2px;
				transition: all 0.2s;
			}
		}
	}
}