.certificate-container {
	margin-bottom: 1em;
	float: left;
	width: 100%;
}

.certificate {
	display: block;
	width: 1500px;
	height: 1025px;
	position: relative;
	// background-color: #fedda8;
	color: rgba(0,0,0,0.7);
	line-height: 1.4;
	// background-image: linear-gradient(0deg, #ffe37f,#ffc794);
	font-family: 'EB Garamond', serif;

	&.style-1 {
		.certificate-title {
			top: 269px !important;
		}

		.certificate-colofon {
			width: 100%;
			left: 0;
			top: 650px;
		}
	}

	&.style-2 {
		height: 1072px;
		background: rgb(244, 242, 239);
	}

	&.style-3 {
		height: 1072px;
		background: rgb(244, 242, 239);
	}

	&.style-4 {
		height: 1072px;
		background: rgb(244, 242, 239);
	}

	&.style-5 {
		height: 1009px;
		background: rgb(244, 242, 239);
	}

	&.style-7 {
		height: 1025px;
		background: white;
	}

	&.style-7 {
		height: 1009px;
		background: white;

		color: rgb(122, 109, 78);
	}

	&.style-8 {
		color: #003399;
		height: 1009px;
		background: #ECE6F3;

		.certificate-title {
			top: 214px !important;
		}
	}

	&.type-training {
		background: white;
	}

	&.type-public {
		// filter: hue-rotate(270deg) saturate(0.6);
	}

	* {
		-webkit-user-select: none;
		user-select: none;
		cursor: default;
	}

	.certificate-backdrop {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.certificate-title,
	.certificate-title-byline,
	.certificate-user-name,
	.certificate-achievement,
	.certificate-colofon {
		position: absolute;
		left:  140px;
		right: 140px;
		text-align: center;
		background: transparent;
	}

	.certificate-organisation-logo {
		position: absolute;
		top: 760px;
		left: 50%;
		transform: translate(-50%, -50%);
		object-fit: contain;
		width: 350px;
		max-height: 230px;
		background-position: center;
	}

	.certificate-title {
		top: 254px !important;
		font-size: 100px !important;
		line-height: 100px !important;
	 	text-transform: uppercase !important;
	}

	.certificate-title-byline {
		top: 400px;
		font-size: 30px !important;
		transform: translateY(-50%);
	}

	.certificate-user-name {
		top: 490px;
		font-size: 70px;
		font-weight: bold;
		line-height: 70px;
		transform: translateY(-50%);
	}

	.certificate-achievement {
		top: 565px;
		font-size: 30px !important;

		.certificate-date {
			white-space: nowrap;
		}
	}

	.certificate-colofon {
		top: 690px;
		width: 80%;
		border: none;
		left: 10%;
		right: 0;
		font-size: 30px !important;

		td {
			height: 92px;
			overflow: hidden;
		}

		th,
		td {
			border: none;
			text-align: center;
			width: 50%;
			box-sizing: border-box;
			display: block;
			float: left;

			&.department {
				padding: 0 9% 0 23%;
			}

			&.tool {
				padding: 0 23% 0 9%;
			}
		}
	}
}