form {
  @include screen-size(small) {
    .initiator { 
      .pat-avatar {
        display: none;
      }
    }
  }

  .image.logo {
    img {
      max-width: 50%;
      display: block;
      margin-bottom: 15px;
    }    

    .pat-button {
      clear: both;
    }
  }

  @media print {
    .initiator { 
      .pat-avatar {
        display: none;
      }
    }
  }

  sup.required {
    color: gray;
    font-weight: normal;
    font-size: 0.8em;

    &:before {
      content: '(';
    }

    &:after {
      content: ')';
    }
  }

  input,
  textarea:not(.pat-content-mirror),
  .textarea,
  select,
  .select2-container .select2-choice,
  .select2-container .select2-choices {
    @media screen { 
      background-color: $colour-form-fields-background; 

      &:focus{
        box-shadow: inset 0 0 1px 1px silver;
      }

      .pat-modal & {
        background-color: rgba(50,50,50,0.1); 
      }
    }
  }

  .select2-container {
    &.select2-container-active {
      .select2-choice,
      .select2-choices {
        box-shadow: inset 0 0 1px 1px silver;
      }
    }

    &.select2-dropdown-open {
      .select2-choice,
      .select2-choices {
        background-color: white;      }
    }
  }

  fieldset {   
    @-moz-document url-prefix() { 
      // display: table-cell; // Fix for min-width issue with Firefox: https://bugzilla.mozilla.org/show_bug.cgi?id=504622#c5

      // Patch above removed in favour of Firefox 86+ which has its own unique bugs.
    }
  }
}