	&#page-module-information {
		background: white;

		.row {
			margin: 0 -1.5rem !important;
		}

		.pat-rich-editor {
			margin: 0;

			.ProseMirror {
				min-height: 200px;
			}
		}
	}

	&#page-module-comments {
		.pat-rich-editor .ProseMirror {
			min-height: 100px;
			overflow: hidden;
		}
	}

	.panel-content {
		@include clear;

		> *:last-child {
			margin-bottom: 0;
		}
	}

	.illustrations {
		float: right;
		width: 30%;
		margin: 5px 0 40px 40px;

		.illustration {
			margin-bottom: 15px;
		}
	}

	.image-uploader {
		min-height: 8em;

		img {
			margin-top: -0.2em;
		}
	}

	h4 {
		font-weight: bold;
		
	}

	.measures {

		&+.button-bar {
			margin-bottom: 1em;
		}
	}

