$colour-toolbar-elements: white !default;
$colour-toolbar-elements-hover: white !default;

$portal-header-shade: dark !default;

$global-header-box-shadow: black !default;
$global-header-box-shadow-intensity: 0.08 !default;
$global-header-box-shadow-size: 9px !default;

@mixin toolbar-active-state() {
	text-shadow: 0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
	             0 0 5px white,
	             0 0 6px white,
	             0 0 7px white;
}

#toolbar {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	background-color: $colour-toolbar;
	color: $colour-toolbar-elements;
	z-index: 2;
	box-shadow: 0 0 $global-header-box-shadow-size 0.5 * $global-header-box-shadow-size rgba($global-header-box-shadow, $global-header-box-shadow-intensity);
	border-bottom: silver 1px solid;

	@media print {
		display: none;
	}

	.title-group {
		position: absolute;
		font-size: 20px;
		top: 15px;
		cursor: default;

		@include screen-size(small) {
			float: left;
			margin-left: 5px;
		}

		@include screen-size(medium-large) {
			left: 50%;
			transform: translateX(-50%);
		}

		&[href] {
			cursor: pointer;
		}

		.logo {
			background: url(../../oira/style/oira-logo.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			color: transparent;
			width: 200px;
			height: 20px;

			@include screen-size(small) {
				background-position: left;
			}

			@include screen-size(medium-large) {
				background-position: center;
			}

			@if $portal-header-shade == light {
     			filter: invert(100%);
     	    }
		}
	}

	padding: 0.61rem 2% 0.61rem 2%;

	@include screen-size(small) {
		overflow: hidden;
		padding: 0.61rem 4% 0.61rem 4%;
	}

	&.injecting {
		> * {
			opacity: 1;
		}

		&:before {
			display: none;
		}
	}
	box-sizing: border-box;
	height: 3rem;

	.warning {
		display: inline-block;
		background-color: $colour-alert;
		padding: 0 0.8em;
		border: rgba(255,255,255,0.6) solid 1px;
		border-radius: 2px;

		a {
			text-decoration: underline;
		}
	}

	> a[href],
	> button {
		&:hover,
		&.tooltip-active-click,
		&.current {
			opacity: 1;
		}
	}

	> a,
	> button {	
		color: $colour-toolbar-elements;
		// padding: 0 0.5em;
		cursor: default;
		display: inline-block;
		line-height: 1.6;
		padding: 0;
		// width: 2rem;
		overflow: hidden;
		display: inline-block;
		float: left;
		height: 1.5em;
		margin-right: 0.8em;
		line-height: 31px;
		opacity: 0.8;
		display: inline-block;
		font-size: 24px;
		
		sup.counter {
	        font-size: 0.6em;
	        width: 1.2em;
	        height: 1.2em;
	        line-height: 1.2em;
	        top: 0px;
	        right: 0;
	        position: absolute;
	        background-color: $colour-alert;
	        text-align: center;
	        color: white;
	        border-radius: 0.6em;

	        &.mutated {
	            animation: bounceIn 0.5s;
	        }

	        &.digit-2 {
	           width: 1.5em; 
	        }

	        &.digit-3 {
	           width: 1.8em; 
	        }
	    }

		&:before {
			margin: 0;
			text-align: center;
			width: 100%;
			font-family: fontello;
		}

		@include screen-size(small) {
			margin-bottom: 1em;
		}

		&#toggle-tree {
			&:before {
				content: '#{$glyph-menu}';

				.sidebar-on & {
					@include toolbar-active-state;
				}
			}

			@include screen-size(medium-large) {
				display: none;
			}
		}

		&#toggle-osc {
			&:before {
				content: '#{$glyph-menu}';

				@include screen-size(large) {
					.osc-on & {
						@include toolbar-active-state;
					}	
				}

				@include screen-size(small-medium) {
					.osc-s-on & {
						@include toolbar-active-state;
					}
				}
			}

			@include screen-size(small) {
				display: none;
			}
		}

		&#more-menu {
			&:before {
				content: '#{$glyph-ellipsis}';
			}

			&.active {
				&:before {
					@include toolbar-active-state;
				}
			}
		}

		&#user-menu {
			&:before {
				content: '#{$glyph-user}';
			}

			&.active {
				&:before {
					@include toolbar-active-state;
				}
			}
		}

		&#messages-menu {
			&:before {
				content: '#{$glyph-icon-chat}';
			}

			&.active {
				&:before {
					@include toolbar-active-state;
				}
			}
		}

		&#help-menu {
			&:before {
				content: '#{$glyph-help-circle}';
			}

			&.active {
				&:before {
					@include toolbar-active-state;
				}
			}
		}
	}

	.user-status,
	.login,
	.registration,
	.help,
	.home,
	#more-menu,
	#user-menu,
	#help-menu,
	#messages-menu {
		float: right;
		margin-left: 0.8em;
		margin-right: 0;
	}

	.home {
		@include screen-size(small) {
			display: none;
		}
	}

}

.osc-off #toolbar #burger {
	// box-shadow: inset 0 0 0.15em rgba(0,0,0,0);

	&:before {
		@include screen-size(large) {
			content: "\e81d";
		}

		@include screen-size(medium) {
			content: "\e81d";
		}

		@include screen-size(small) {
			content: "\e81b";
		}
	}
}

@include screen-size(small) {
	.focus-sidebar {
		#toolbar .icon-left {
			display: none;
		}

		#toolbar #burger {
			display: inline-block;

			&:before {
				font-size: 1em;
				width: 1.6rem;
				line-height: 1.7rem;
				float: left;
			}
		}
	}

	.osc-s-off {
		#toolbar #burger {
			&:before {
				content: "\e812";
			}
		}
	}

	.osc-s-on {
		#toolbar #burger {
			&:before {
				content: "\e81b";
			}
		}
	}

