@charset "UTF-8";

.image-uploader {
  position: relative;
  overflow: hidden;
  min-height: 1.4em;
        
  &:before {
    top: 0;
    bottom: 0;
    display: block;
    content: ' ';
    background-color: $colour-base;
    position: absolute;
    z-index: 0;
    border-radius: $border-radii;
  }

  &:after {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -1em 0 0 -1em;
    width: 2em;
    height: 2em;
    text-align: center;
    line-height: 2em;
    display: block;
    content: '#{$glyph-picture}';
    font-family: fontello;
    z-index: 0;
    font-size: 2em;
    color: rgba(0,0,0,0.5);
  }

  &.columns:before {
    left: 1.5rem;
    right: 1.5rem;
  }

  .image-upload-remove-button {
    position: absolute;
    top: 11px;
    right: 31px;
    width: 30px;
    height: 30px;
    overflow: hidden;
    color: transparent;
    z-index: 2;
    border-radius: 50%;
    background: transparent;

    &:hover {
      &:before {
        background-color: rgba(0,0,0,0.4);
      }
    }

    &:before {
      width: 30px;
      height: 30px;
      text-align: center;
      line-height: 30px;
      border-radius: 50%;
      background-color: rgba(0,0,0,0.2);
      color: white;
      margin: 0;
      font-size: 18px;
    }
  }

  .upload-button {
    @include pat-button();
    position: absolute;
    z-index: 1;
    left: 17%;
    right: 17%;
    bottom: 0.7em;
    text-align: center;
    text-overflow: ellipsis;
    overflow: hidden;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 1.5;
    font-size: 0.9em;
    height: auto;
    background-color: rgba(0,0,0,0.2);
  }

  img ~ .upload-button {
    color: white;
  }

  input[type="file"] {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    z-index: 1;
  }

  img {
    width: 100%;
    float: left;
    position: relative;
    z-index: 1;

    &+.upload-button {
      color: white;
      border: 1px solid rgba(255,255,255,0.7);
      background: rgba(0,0,0,0.6);
    }
  }
}