$cover-slide-backgound: none !default;

.training-card {
	background: white;
	padding: 60px;
	border-radius: $border-radii;
    box-shadow: $default-box-shadow;
    @include clear;
	margin-bottom: 20px;
	position: relative;
	overflow: hidden; // Prevent Header with negative top margin (for proper auto-scroll behaviour) to bleed over content (with links) of previous card.
	max-width: 800px;
	margin-left: auto;
	margin-right: auto;
	box-sizing: border-box;
	
	.card-hero {
		margin-bottom: 20px;
		
		img {
			max-height: 400px;
			width: 100%;
			object-fit: cover;
		}
	}

	.training-card-section {
		padding: 0;

		@media print {
			padding: 0;
			border: none;
		}

		.card-illustrations {
			float: right;
			width: 30%;
			margin: 5px 0 40px 40px;

			.illustration {
				margin-bottom: 15px;
			}
		}

		.card-subtitle {
			margin-top: 0;
			margin-bottom: 15px;
			padding: 0;
			font-size: 25px;
		}

		.card-function-bar {
			position: absolute;
			top: 18px;
			right: 18px;

			.card-edit-button {
				height: 20px;
				width: 20px;
				cursor: default;
				float: right;
				margin-left: 10px;
				position: relative;
				color: transparent;
				background: rgba(black, 0.1);
				border-radius: 0.7 * $border-radii;

				&:before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					line-height: 20px;
					font-size: 12px;
					display: block;
					font-family: fontello;
					color: rgba(black, 0.55);
					content: '#{$glyph-icon-edit}';
					text-align: center;
				}

				&:hover {
					&:before {
						color: black;
					}
				}
			}

			.card-number {
				display: inline-block;
				background: rgba(black, 0.1);
				padding: 0 12px;
				line-height: 20px;
				height: 20px;
				font-size: 12px;
				border-radius: 0.7 * $border-radii;
				color: rgba(black, 0.55);
				font-family: monospace;		
				font-weight: bold;	
				letter-spacing: -1.5px;	
				float: right;
			}
		}

		.card-title {
			box-sizing: border-box;
			// height: 219px;
			font-weight: normal;
			overflow: hidden;
			padding: 0;
			margin-top: -200px;
			padding-top: 200px;
			margin-bottom: 30px;
			clear: both;

			.card-title-label {
				display: block;
				font-stretch: condensed;
				font-size: 38px;
				line-height: 1.15;
			}
		}
	}
}