@charset "UTF-8";

$post-box-border-radius: $border-radii;

.application-home #post-box {
  margin: 0 auto 1em;
  display: block;

  @include screen-size(large) { 
    width: 85%;
  }
}

.update-social {
  position: relative;
  background-color: $colour-form-fields-background;
  border-radius: $post-box-border-radius;

  &#new-post-box {
    display: none;
  }

  @media print {
    display: none;
  }

  textarea,
  .content-mirror {
      margin: 0;
      padding-right: 2em;
      padding-left: 0.5em;
      padding-top: 0.4em;
      padding-bottom: 0.4em;
      line-height: 1.5em;
      float: left;
      // height: 2.3em;
      border: none;
      overflow: hidden;

      @include screen-size(large) { 
        background: transparent;
      }
      
      resize: none;
      width: 100%;
      // -webkit-transition: height 0.4s;
      // -moz-transition: height 0.4s;
      // transition: height 0.4s;

      &:focus {
        outline: none;
        box-shadow: none;
      }
  }

  textarea {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      height: 100%; // Needed for Firefox
      -moz-transition: padding 0.3s;
      -webkit-transition: padding 0.3s;
      transition: padding 0.3s;
      -moz-text-fill-color: transparent;
      -webkit-text-fill-color: transparent;
      text-fill-color: transparent;

      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: rgba(0,0,0,0.5);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: rgba(0,0,0,0.5);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: rgba(0,0,0,0.5);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: rgba(0,0,0,0.5);
      }
  }

  .content-mirror {
      color: black;
      font-family: inherit;
      cursor: default;
      text-align: start;
      white-space: pre-wrap;
      vertical-align: text-bottom;
      overflow: visible;
      width: 100%;
      box-sizing: border-box;
      -moz-transition: all 0.3s;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      // margin-bottom: -100%;
      .text .placeholder {
          font-style: normal;
          color: rgba(0,0,0,0.5);
      }

      .selected-users,
      .selected-tags {
          position: relative;
          z-index: 2;
          color: #222;
          font-weight: bold;
          font-style: normal;
      }
  }

  &.status-active {
    box-shadow: $default-focus-marking;
  }

  &.status-active textarea,
  &.status-attach textarea,
  &.status-active .content-mirror,
  &.status-attach .content-mirror {
    padding-top: 1em;
    padding-bottom: 1em;
  }

  &.status-active .content-mirror,
  &.status-attach .content-mirror {
    // overflow: auto;
    padding-bottom: 2.5em;
  }

  &.status-active textarea,
  &.status-attach textarea {
    bottom: 2.6em;
  }

  .icon-attach {
      color: #666;

      &:hover, 
      &:active {
          color: black;
      }

      cursor: default;
      position: absolute;
      z-index: 2;
      height: 1.5em;
      right: 0.5em;
      top: 0;
      padding-top: 0.4em;
      padding-bottom: 0.4em;
      line-height: 1.5em;

      input {
        position: absolute;
        opacity: 0;
        left: 0;
        top: 0;
      }
  }

  .attachments {
    clear: both;
    margin: 0 !important; //is !important to override Plone style

    .attachment-previews {
        position: relative;
        z-index: 1;
        margin: 0 !important;  //is !important to override Plone style
        clear: both;
        padding: 0 1em;
        font-style: italic;
        color: rgba(0,0,0,0.3);
        height: 0;
        overflow: auto;
        white-space: nowrap;
        transition: all 0.3s;

        @include injection;

        img {
          // height: 100%;
          // display: inline-block;
          // margin-right: 15px;
          // box-shadow: 0 0 2px rgba(0,0,0,0.5)
        }
      }
  }

  .shared-item {
    padding: 0 1em 0.5em 1em;
    overflow: hidden;
    // border-left: 0.3em #efefef solid;

    .preview {
      width: 36%;
      float: left;
      padding-top: 0.3em;

      img {
        background-color: white;
        border: 1px silver solid;
        width: 100%; // fix for IE & FF http://stackoverflow.com/questions/14550356/image-mysteriously-ignoring-max-width-in-firefox-ie#comment20355324_14578128
      }
    }

    &.has-preview {

      .summary {
        width: 60%;
        float: right;
      }
    }

    .title {
      margin-bottom: 0;
      font-size: 0.8em;
      line-height: 1.4em;
    }

    .byline {
      font-size: 0.8em;
      line-height: 1.4em;
      font-style: italic;
      margin-bottom: 0.5em;
    }

    .description {
      font-size: 0.8em;
      line-height: 1.4em;
      font-style: normal;
      color: gray;
      margin-bottom: 0.5em;
    }

    .share-quote {
      font-size: 0.8em;
      line-height: 1.4em;
      border-left: solid 0.3em silver;
      padding-left: 0.5em;
      font-style: italic;
      color: gray;
      margin-bottom: 0.5em;
    }
  }

  &.status-attach
  .attachment-previews {
    min-height: 2em;
    height: auto;
    padding: 1em;
  }

  .button-bar {
    background-color: rgba(0, 0, 0, 0.08);
    height: 0;
    padding: 0 0.5em;
    overflow: hidden;
    margin: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    z-index: 2;
    position: relative;
    border-bottom-left-radius: $border-radii;
    border-bottom-right-radius: $border-radii;

    label.visibility {
      color: #666;
      &:hover,&.focus { color: black }
    }

    .iconified {
      line-height: 25px;

      &.tooltip-active-click {
        color: black;
      }
    }

    .icon-users, 
    button, 
    label.visibility,
    label.response-required  {
      float: left;
      margin: 0 0.5em 0.2em 0;
      border-radius: 0.6 * $border-radii;
      line-height: 26px;

      [lang=de] &,
      [lang=de-de] &,
      [lang=de-ch] & {
        font-size: 12px;
        padding-left: 8px;
        padding-right: 8px;
      }
    }

    br {
      display: none
    }
  }
  &.status-active,
  &.status-attach {
    .button-bar {
    height: 25px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    }
  }

  &.question {
    &.status-active,
    &.status-attach {
      .button-bar {
        height: auto;
      }
    }
  }
}

.application-workspace #post-box,
.application-workspaces #post-box,
.application-workspace .post-box,
.application-workspaces .post-box  {
  width: 80%;
  display: block;
  margin: 2em auto;

  @include screen-size(small) {
    width: 90%;
    margin: 0 auto 2em;
  }
}

