dl.status {
  // padding: 4%;
  padding-top: 1em;
  // background: rgba(0,0,0,0.1);
  margin-bottom: 2em;
  margin-top: 0;

  dt {
    // height: 2em;
    margin-bottom: 12px;
    // line-height: 2em;
    // margin-bottom: -2em;
    color: white;
    // padding-left: 3%;
    position: relative;
    clear: left;
	  display: block;
    text-decoration: none;
    font-weight: bold;
    // margin-left: 6em;

    a {
      color: black;
      text-decoration: none;
      display: block;

      &:after {
        font-family: fontello;
        padding-left: 0.5em;
        content: '#{$glyph-right-big}';
        text-decoration: none !important;
        display: inline-block;
        transition: all 0.2s;
      }

      &:hover {
        text-decoration: underline;

          &:after {
            transform: translateX(5px);
            text-decoration: none !important;
          }
      }
    }
  }

  dd {
    // margin-left: 6em;
    margin-left: 0;
    position: relative;

    .high-prio-risks {
      margin-bottom: 3.5em;

      h4 {
        color: rgba(0,0,0,0.6);
      }

      li {
         margin-left: 0;
         padding-left: 1em;

         a {
          color: rgba(0,0,0,0.6);

          &:hover {
            color: black;
            text-decoration: underline;
          }
         }

         &:before {
          content: "";
          float: left;
          margin-left: -1em;
         }
      }
    }

    .legend {
      margin-bottom: 3.5em;
      color: rgba(0,0,0,0.6);

      li {
        padding: 0.2em 0 0.2em 1.8em;
        font-style: normal;

        .number {
          float: left;
          margin-left: -1.8em;
          margin-top: -0.1em;
          width: 1.5em;
          height: 1.5em;
          line-height: 1.6em;
          border-radius: 50%;
          text-align: center;
          background-color: gray;
          color: white;
          font-size: 0.8em;
          font-weight: normal;
        }

        &.answered .number {
          background-color: green;
        }

        &.risk .number {
          background-color: red;
        }

        &.risk-measure .number {
          background-color: DarkRed;
        }

        &.postponed .number {
          background-color: orange;
          color: rgba(0,0,0,0.6);
        }

        &.unanswered .number {
          background-color: rgba(0,0,0,0.1);
          color: rgba(0,0,0,0.6);
        }

        &:before {
          display: none;
        }
      }
    }

    .graph {
      background: rgba(0,0,0,0.1);
      clear: left;
  	  display: block;
      height: 2em;
      margin: 0 0 1.5em 0;
      padding: 0;
      text-align: center;
      color: white;
      line-height: 2.1em;
      border-radius: 1em;
      overflow: hidden;

      .range {
        height: 2em;
        overflow: hidden;
        display: inline-block;;
        text-align: center;
        float: left;

        .label {
          display: none;
        }

        .number {  
          font-weight: normal;
          font-size: 0.8em;
        }

        &.answered {
          background-color: green;
        }

        &.unanswered {
          color: rgba(0,0,0,0.6);
        }

        &.risk {
          background-color: red;
        }

        &.risk-measure {
          background-color: DarkRed ;
        }

        &.postponed {
          background-color: orange;
          color: rgba(0,0,0,0.6);
        }
      }
    }
  }
}