$cover-slide-backgound: none !default;

.presentation-placeholder {
	z-index: 10000;
	opacity: 0;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	position: fixed;
	pointer-events: none;
	width: 100%;
	height: 100%;

	.presentation-slide {
		margin: 0;
	}
}

// .pat-message + .presentation-slide {
// 	margin-top: 30px;
// }

.presentation-slide {
	margin-bottom: 20px;
	transition-delay: 1s;
	transition: opacity 1s;
	cursor: default;
	max-width: 800px;
	margin-left: auto;
	margin-right: auto;
	box-sizing: border-box;
	margin-top: 6%;

	// + .pat-message & {
	// 	margin-top: 40px;
	// }

	.pat-rich {
		-webkit-user-select: none;
		user-select: none;
	}

	&.stand-alone {
	    .auto-scale-wrapper {
	    	.pat-button {
	    		display: none;
	    	}
	    }
	}

	&.concatenate {
