	.auto-scale-wrapper {
		// Styling on auto scale wrapper to patch background bleeding issue on iPad OS 13.
		border-radius: $border-radii;
		overflow: hidden;
		// border: 1px silver solid;
		background: white;
    	box-shadow: $default-box-shadow;
	}

	@mixin fullscreen {
		width: 100%;
		height: 100%;
		background-color: black;
		-webkit-pointer-events: all;
		pointer-events: all;

		.pat-button {
			-webkit-pointer-events: all;
			pointer-events: all;
		}

		.pat-button#assessments-training-start-button,
		.pat-button#assessments-questions-start-button
		 {
			display: inline-block;
		}

		.slide-content {	
			height: 1080px;

			.slide-body {
				overflow: auto;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
			}

			.slide-footer {
				display: block;
			}
		}

		.auto-scale-wrapper {
			background-color: white;
			top: 50%;
			left: 50%;
			position: relative;
			transform: translate(-50%,-50%);
			border: none;
		}
	
	}

	&:-webkit-full-screen {
		@include fullscreen;
	}

	&:-ms-fullscreen {
		@include fullscreen;
	}

	&:fullscreen {
		@include fullscreen;
	}

	.pat-collapsible & {
		opacity: 0;
	}

	.pat-collapsible.open & {
		animation: fadeIn 1s;
		animation-delay: 0.2s;
		animation-fill-mode: forwards;
	}

	.slide-content {
		width: 1920px;

		.button-bar {
			.pat-button {
				font-size: 60px;
				line-height: 95px;
				padding: 0 1.5em;
				border-radius: 2.5 * $border-radii;
				// display: none;
				text-decoration: none !important;
			}
		}

		.slide-body {
			overflow: hidden;

			.slide-illustrations {
				float: right;
				width: 30%;
				margin: 12px 0 60px 60px;

				.illustration {
					margin-bottom: 40px;
				}
			}

			h1.slide-title {
				font-size: 90px;
				line-height: 1.15;
				box-sizing: border-box;
				// height: 219px;
				font-weight: normal;
				overflow: hidden;
				margin-bottom: 50px;

				.slide-number {
					display: none;
				}
			}
		}

		.toggle {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			position: absolute;
			background-color: rgba(255,255,255,0.8);
			background-color: rgba(0,0,0,0.4);
			transition: opacity 0.5s;
			color: transparent;
		}

		&.mode-view {
			.toggle {
				display: none;
			}

			.measure-list {
				display: block;
			}

			.edit-measures {
				display: none;
			}
		}

		&.mode-edit-measures {
			.toggle {
				animation: fadeIn 2s;	
				z-index: 1;
			}

			.measure-list,
			.pat-content-mirror {
				display: none;
			}

			.training-notes {
				z-index: 0;
			}

			.slide-measures {
				.edit-measures {
					display: block;
					z-index: 1;
					background: white;
					position: relative;

				}
			}
		}

		&.mode-view {
			&:hover {			
				.training-notes-editor:not(.injecting):before {
					position: absolute;
					z-index: 1;
					top: -28px;
					right: -28px;
					bottom: -28px;
					left: -28px;
					outline: 4px solid lightblue;
					content: ' ';
					display: block;
					// pointer-events: none;
				}

				.measure-list {
					outline: 4px solid lightblue;
				}
			}
		}

		.slide-measures {
			// margin-bottom: 40px;
			margin-bottom: 0;

			.measure-list {
				margin: -12px;
				padding: 12px;
				position: relative;
			}

			.edit-measures {
				background-color: white;
				padding: 16px;
				margin: -16px;
				position: relative;
				z-index: 1;
				max-height: 695px;
				overflow: auto;
				display: none;

				&.pat-checklist {
					label {
						color: gray;

						&.checked {
							color: black;

							&:before {
								content: '#{$glyph-eye}';
							}
						}

						&.unchecked {
							// text-decoration: line-through;

							&:before {
								content: '#{$glyph-eye-off}';
								text-decoration: none;
							}
						}
					}
				}
			}
		}

		.slide-body {
			font-size: 48px;
			padding: 100px 99px 100px;
			// margin-top: -12px;
			line-height: 64px;
			// height: 777px;

			h1,
			h2,
			h3,
			h4,
			h5 {
				font-size: 50px;
				font-weight: bold;
				margin-bottom: 40px;
				line-height: 64px;
				padding: 0;
			}

			blockquote {
				padding-left: 20px;
				border-left: 10px silver solid;
				color: #666;
				font-style: italic;
			}

			table {
				background: transparent;
			}

			ul,
			ol,
			table,
			p,
			dl {
				margin-bottom: 40px;
			}

			h4,
			h5 {
				margin-bottom: 0;
			}	

			.content-mirror {
				margin-bottom: 12px;
				opacity: 0;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				z-index: 10;			

				.notes,
				.notes + textarea {
					line-height: 64px;
					padding-bottom: 50px;
					margin: 0;
					color: black;
				}

				.notes .output {
					margin-bottom: 50px;
				}
			}

			.training-notes-editor {
				position: relative;
			}
		}

		.slide-footer {
			border-top: 2px solid silver;
			color: black;
			position: absolute;
			left: 99px;
			right: 99px;
			bottom: 0;
			height: 100px;
			white-space: nowrap;
			line-height: 100px;
			font-size: 24px;
			background: white;
			display: none; // Hide in normal view.

			.colofon {
				float: left;

				strong {
					font-weight: normal;
				}
			}

			.controls {
				float: right;

				.pat-button {
					line-height: 64px;
					font-size: 34px;
					margin: 18px 0 0 30px;
					text-decoration: none !important;

					&.back {
						width: 40px;
						color: transparent !important;
						text-align: center;
						overflow: hidden;
						padding: 0;
						position: relative;

						&:before {
							font-family: fontello;
							width: 100%;
							height: 100%;
							position: absolute;
							top: 0;
							left: 0;
							color: black !important;
							content: '#{$glyph-angle-left}';
							// font-size: 28px;
							@include screen-size(medium-large) {
								// line-height: 48px;
							}
						}
					}
				}
			}
		}

