.download-blocks {
	margin: 40px 0;

	.download-block {
		margin-bottom: 20px;
		box-shadow: $default-box-shadow;
		background-color: $colour-portlet-background;
		border-radius: $border-radii;
		padding: 5%;

		.download-block-header {
			overflow: hidden;
			margin-bottom: 30px;
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			gap: 30px;

			.pat-button {
				// float: right;
				margin: 0;
				width: auto;
				overflow: visible;
			}

			h2 {
				// float: left;
				padding-top: 0;
				font-size: 1.5em;
				margin: 0;
				hyphens: auto;
				-webkit-hyphens: auto;
				overflow: hidden;

				&:before {
					font-size: 0.8em;
					margin-right: 0.4em;
					position: relative;
					top: -0.1em;
					margin-left: 0;
				}
			}
		}

		.download-block-body {
			> *:last-child {
				margin-bottom: 0;
			}
		}
	}
}