.content-mirror {
	position: relative;
	z-index: 1;

	.presentation-slide & {
		margin-bottom: 0;
	}

	&.focus,
	&:hover,
	.mode-edit & {
		&:before {
			position: absolute;
			z-index: -1;
			top: -10px;
			right: -10px;
			bottom: -10px;
			left: -10px;
			outline: 4px solid lightblue;
			content: ' ';
			display: block;

			.presentation-slide & {
				background: white;
				outline: none;
			}
		}
	}

	textarea,
	.output {
        font-family: $body-font-family;
        line-height: inherit;
        font-size: inherit;
        font-weight: inherit;
        font-style: inherit;
	    font-stretch: inherit;
        white-space: pre-line;
	}

	.output {
	    font-family: inherit;
	    cursor: text;
	    text-align: start;
	    white-space: pre-line;
	    vertical-align: text-bottom;
	    overflow: visible;
	}

	.placeholder {
		font-style: italic;
		color: silver;
	}

	textarea {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		margin: 0;
		background: transparent;
		border: none;
		resize: none;
		padding: 0;
		overflow: hidden;
        -moz-text-fill-color: transparent;
        -webkit-text-fill-color: transparent;
        text-fill-color: transparent;
        outline: none !important;
        box-shadow: none !important;

        color: rgba(0,0,0,0); // Required for IE

        @supports (text-fill-color: transparent) {
        	color: black; // Required to show the cursor again that got hidden because of the above measure which is required for IE.
		}

        @supports (-webkit-text-fill-color: transparent) {
        	color: black; // Required to show the cursor again that got hidden because of the above measure which is required for IE.
		}
	}
}