#appendix {
	padding: 2em 0 0;
	font-size: 0.9em;
	text-align: center;

	.appendix-segment {
		display: inline-block;
		padding: 0 10px;
		border-left: 1px solid white;
		text-decoration: none;
		color: rgba(white, 0.8);

		&:hover {
			color: rgba(white, 1);
			text-decoration: underline;
		}

		&:first-child {
			border-left: none;
		}
	}

	img {
		display: inline;
		height: 0.75em;
	}
}