.page-actions {
	text-align: right;

	@include screen-size(medium-large) {
		position: absolute;
		top: 1.5rem;
		right: 1.5rem;
	}

	@include screen-size(small) {
		display: block;
		padding-bottom: 1em;
	}

	> a,
	> button {
		color: gray;
		margin-left: 1em;

		&[href]:hover {
			color: black;
		}

		&.active {
			color: black;
		}
	}
}