.image-collage {
	margin: 0 -5px 1em;
	overflow: hidden;

	&.float-before {
		@include screen-size(medium-large) {
			width: 30%; 
			float: left;
			margin: 0 15px 15px 0;

			a {
				padding: 0 5px 5px 0;
			}
		}

		@include screen-size(small) {
			width: 100%; 
			margin-bottom: 15px;
		}
	}

	a {
		float: left;
		padding: 5px;
		box-sizing: border-box;

		@include screen-size(small) {
			&:not(:first-child) {
				display: none;
			}

			&:first-child {
				width: 100%;
				padding: 0;

				img {
					height: 180px;
				}
			}
		}
	}

	&.count-1 {
		a {
			@include screen-size(medium-large) { width: 100%; }
		}
	}

	&.count-2 {
		a {
			@include screen-size(medium-large) {width: 50%; }
		}
	}

	&.count-3 {
		a {
			@include screen-size(medium-large) {width: 50%; }

			&:first-child {
				width: 100%;
			}
		}
	}

	&.count-4 {
		a {
			@include screen-size(medium-large) {width: 33.333332%; }

			&:first-child {
				width: 100%;
			}
		}
	}
}

