$sidebar-toggle-height: 70px !default;

#toggle-sidebar {
    position: absolute;
    bottom: 50px;
    height: $sidebar-toggle-height;
    width: 32px;
    margin: 0;
    left: -3px;
    transition: opacity 0.3s;
    z-index: 10;
    overflow: hidden;

    .module-workspace-home & {
        opacity: 0;
    }

    @media print {
        display: none;
    }

    .slideshow-running & {
        display: none;
    }

    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;

    border-top: white 2px solid;
    border-right: white 2px solid;
    border-bottom: white 2px solid;

    .application-workspace &,
    .application-workspaces & {
        border-radius: 50%;
        border: white 2px solid;
    }


    .sidebar-left-open & {
        border-top: $colour-base 2px solid;
        border-right: $colour-base 2px solid;
        border-bottom: $colour-base 2px solid;
    }

    padding: 0;
    background: var(--colour-accent);

    &:hover {
        background: darken($colour-accent, 5%);
    }

    z-index: 1;

    .application-workspace &,
    .application-workspaces & {
        left: 34px;
    }

    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;

    @include screen-size(small) {
        display: none;
    }

    &:before {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        font-family: fontello;
        content: '#{$glyph-icon-arrow-left}';
        transition: all 0.3s;
        font-size: 12px;
        line-height: $sidebar-toggle-height;
        text-align: center;
        color: white;
        text-indent: -1px;
        transform: rotate(-180deg);

        @include screen-size(large) {
            .sidebar-left-open & {
                transform: rotate(0deg);
            }
        }

        @include screen-size(medium) {
            .sidebar-left-open.focus-sidebar & {
                transform: rotate(0deg);
            }
        }
    }

    .open,
    .close {
        position: absolute;
        top: 0;
        left: 0;
        display: none;
        text-indent: -2000px;
        // border-radius: 50%;

        width: 100%;
        height: 100%;

        &:active {
            box-shadow: inset 0 0 0.3em rgba(0, 0, 0, 0.5);
        }
    }

    .open {
        .sidebar-left-closed & {
            display: block;
        }

        @include screen-size(medium) {
            .sidebar-left-open.focus-document & {
                display: block;
            }
        }
    }

    .close {
        @include screen-size(large) {
            .sidebar-left-open & {
                display: block;
            }
        }

        @include screen-size(medium) {

            .sidebar-left-open.focus-sidebar &,
            .sidebar-left-open.focus-canvas & {
                display: block;
            }

            &:after {
                display: block;
                content: ' ';
                position: fixed;
                top: 0;
                right: 0;
                width: 40%;

                .sidebar-large & {
                    width: 0;
                }

                background-color: $colour-accent;
                bottom: 0;
                z-index: 1;
            }
        }
    }
}