body.report {
	// margin: 12% 10%;
	margin: 0;
	padding: 12% 10%;
	overflow: auto;
	width: auto;
	height: auto;

    font-size: 80%;

	article.report {
		section.module {
			margin-bottom: 5em;
		}

		p.date {
			font-size: 0.6em;
			color: gray;
			float: right;
			padding-left: 2em;
		}

		h1 {
			margin-bottom: 0.3em;
		}

		h1 + .byline {
			margin-bottom: 4em;
		}

		.title-line {
			td,
			th {
				padding: 2em 0 1em 0;
			}
			h3 {
				font-weight: bold;
				margin-bottom: 0;
				font-size: 1.3em;
			}
		}

		.prio-high {
			.risk-title {
				color: red;
			}

			td:before {
				background-color: red;
			}
		}

		.prio-medium {
			.risk-title {
				color: orange;
			}

			td:before {
				background-color: orange;
			}
		}

		.prio-low {
			.risk-title {
				color: green;
			}

			td:before {
				background-color: green;
			}
		}

		table {
	    	border-collapse: collapse;

			th,
			td {
				border-bottom: 1px solid silver;
				padding: 0.5em 1em 0.5em 0;
			}

			td {
				padding-left: 0.5em;
				padding-right: 0.5em;
				position: relative;

				&.start,
				&.ongoing,
				&.end,
				&.start-end {
					color: transparent;

					&:before {
						display: block;
						position: absolute;
						content: ' ';
						height: 20px;
						top: 50%;
						transform: translateY(-50%);
					}
				}

				&.ongoing:before {
					width: 100%;
					left: 0;
					right: 0;
				}

				&.start:before {
					width: 73%;
					left: auto;
					right: 0;
					border-radius: 10px 0 0 10px;
				}

				&.end:before {
					width: 73%;
					left: 0;
					right: auto;
					border-radius: 0 10px 10px 0;
				}

				&.start-end:before {
					width: 20px;
					left: auto;
					right: 50%;
					margin-right: -10px;
					border-radius: 50%;
				}
			}

			thead {
				th {
					text-align: center;
				}
			}

			tbody {
				td {
					vertical-align: middle;
					text-align: center;
					font-weight: bold;
					font-size: 3em;

					&:nth-child(even) {
						background-color: rgba(0,0,0,0.06);
					}
				}	
			}
		}

		.risk-title {
			margin-bottom: 0;
			font-weight: bold;
		}

		.description {
			font-weight: normal;
			margin-bottom: 0;
		}

	}
}