@charset "UTF-8";

@mixin checkbox() {
	padding-left: 1.5em;
	position: relative;
	
	&.checked,
	&.unchecked {


		&.label-fragment-title {
			padding-left: 0;
			
			&:before,
			&:after {
				left: -1.5em;
			}
		}

		&:before {
			font-family: fontello;
			content: '#{$glyph-checkbox}';
			float: left;
			position: absolute;
			left: 0;
			// top: 0em;
		}
	}

	&.disabled {
		color: rgba(0,0,0,0.3);
	}

	&.unchecked {
		& + .label-fragment-description {
			display: none;
			position: absolute;
			bottom: -20px;
			transform: translateY(100%);
			left: 1.5em;
			right: 1em;
		    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
		    padding: 1em;
			margin: 0;
			background-color: white;
			box-sizing: border-box;
			border-radius: $border-radii;
			z-index: 1;
			pointer-events: none;
			animation: fadeIn 0.25s;

		    &:before {
		    	content: ' ';
		    	height: 20px;
				width: 20px;
				top: -11px;
				display: block;
				background-color: white;
				border: 1px solid silver;
				z-index: 0;
				position: absolute;
				transform: rotate(45deg);
				box-shadow: rgba(0,0,0,0.19922) 0px 0px 10px;
		    }

		    &:after {
		    	content: ' ';
				position: absolute;
				background-color: white;
				top: 0;
				right: 0;
				left: 0;
				display: block;
				height: 1em;
				z-index: 1;
				border-radius: $border-radii;
		    }
		}

		&:hover {
			& + .label-fragment-description {
				display: block;
			}
		}

	}

	.label-fragment-description {
		// color: #666;
		font-size: 0.9em;
		line-height: 1.4;

		> *:last-child,
		div > *:last-child {
			margin-bottom: 0;
		}
	}

	&.checked {
		& + .label-fragment-description {
			display: block;
			margin-bottom: 8px;
		}

		&:before {
			content: '#{$glyph-checkbox-active}'
		}
	}

	input[type="checkbox"],
	input[type="radio"] {
		opacity: 0;
		position: absolute;
		left: 0;
		top: 0.55em;
	}
}

label.pat-checklist {
	@include checkbox();

	padding-left: 1.1em;
	padding-right: 0.5em;
	display: inline-block;
}

.pat-checklist:not(.free-form) {
	p {
		margin-bottom: 10px;
	}

	> br {
		display: none;
	}

	&:not(.free-form) {
		label,
		> .label {
			display: block;
			position: relative;
			@include checkbox();
			@include clear();

			&:before {
				z-index: 1;
			}
		
			&:after {
				position: absolute;
				left: 0;
				top: 0.2em;
				width: 0.75em;
				height: 0.75em;
				content: ' ';
				display: block;
				background-color: white;
				border-radius: 4px;
				z-index: 0;
			}
		}

		&.show-and-hide
			label {
				color: gray;

				&:before {
					color: #666;
				}

				&:hover {
					&:before {
						color: black;
					}
				}

				&.checked {
					color: black;

					&:before {
						content: '#{$glyph-eye}';
					}
				}

				&.unchecked {
					// text-decoration: line-through;

					&:before {
						content: '#{$glyph-eye-off}';
						text-decoration: none;
					}
				}
			}

	}

	&.cloud {
		label {
			display: inline-block;
			margin-right: 1em;
		}
	}

	&.radio,
	&.radio-bars {
		label,
		> .label {
			&:before {
				z-index: 1;
			}
			
			&:after {
				position: absolute;
				left: 0;
				top: 0.3em;
				width: 0.8em;
				height: 0.8em;
				content: ' ';
				display: block;
				background-color: transparent;
				border-radius: 50%;
				z-index: 0;
			}

			&.unchecked:before {
				content: '#{$glyph-radio-button}';
			}

			&.checked:before {
				content: '#{$glyph-radio-button-active}';
			}
		}

		.byline {
			margin-top: -13px;
			margin-bottom: 13px;
			font-size: 0.8em;
			color: gray;
		}
	}

	label,
	> .label {
		textarea,
		.pat-rich-editor {
			margin: 0.2em 0;
			width: 100%;
			color: black;
		}

		&.disabled:before {
			opacity: 0.6;
		}

		form fieldset.vertical & {
			input {
				width: auto;
				display: inline;
				margin: 0;
			}
		}
	}

	fieldset.composed {
		padding-left: 0;
		margin-bottom: 0;
		position: relative;
	}

	.legend,
	legend {
		.measures-sufficient { 
			display: none;
		}
	}

	.measures-in-place.has-value ~ & {
		.measures-sufficient  {
			display: block;
			opacity: 0;
			animation: fadeIn 1s;
			animation-delay: 0.5s;
			animation-fill-mode: forwards;
		}

		.risk-present {
			display: none;
		}
	}

	&.pat-clone {

		label.clone,
		.label.clone {			
			&:before,
			&:after {
				display: none;
			}

			.remove-clone {
				position: absolute;
				top: 4px;
				left: -2px;
				// display: none;
				background-color: transparent;
				font-size: 1em;

				&:before {
					content: '#{$glyph-ok}';
					color: rgba(black, 0.6);
				}
			}

			&:hover {
				.remove-clone {
					&:before {
						content: '#{$glyph-trash}';
					}

					&:hover {
						&:before {
							color: black;
						}
					}
				}
			}
		}

		&.add-measure-in-place-other {
			display: none;
			animation: fadeIn 1s;

			& + .button-bar {
				display: none;
			}

			&.has-value {
				display: block;

				& + .button-bar {
					display: block;
				}
			}
		}

		&.has-value {
			.legend {
				display: none;
			}
		}
	}

    .additional-meta-data {
        position: relative;
        float: right;
        width: 70%;
        right: -100%;
        top: 0;
        opacity: 0;
        -moz-transition: all 1s;
        -webkit-transition: all 1s;
        transition: all 1s;
        height: 0;

        .sidebar-large & {
            right: 0%;
            opacity: 1;
            height: auto;
        }
    }

    .meta-column {
        width: 0%;
        overflow: hidden;
        -moz-transition: width 1s;
        -webkit-transition: width 1s;
        transition: width 1s;
        float: right;
        padding: 0 0.5em;
        margin: 0;
        @include box-sizing();

        .item-selector & {
        	padding: 1.2em 0.5em 1em;
        }

        &.tag-cloud {
            width: 40%;
        }

        .sidebar-large & {
            width: 30%;

            &.tag-cloud {
                width: 40%;
            }
        }
    }
}