.tippy-tooltip {
	ul.menu {
		@include screen-size(medium-large) {
			.menu-item-sidebar {
				display: none;
			}
		}

		@include screen-size(small) {
			.menu-item-toggle-on-screen-help {
				display: none;
			}
		}
	}

	&.rich {
		.footer {
			background-color: rgba(255,255,255,0.8);
			margin: 0 -1em -1em -1em;
			padding: 1.5em;
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;

			.icon-lightbulb:first-child:before {
				text-align: left;
				width: 0.8em;
			}
		}

		p.footer {
			margin: 0 -1em -1em -1em; // Override pat-rich values.
		}

		h3 {
			font-weight: bold;
		}
	}
}

.tooltip-container {
	ul.menu {
		@include screen-size(medium-large) {
			.menu-item-sidebar {
				display: none;
			}
		}

		@include screen-size(small) {
			.menu-item-toggle-on-screen-help {
				display: none;
			}
		}
	}

	&.toc {
		.tippy-box {
			max-width: 100% !important;
		}

		.tippy-content {
			width: 640px;
			max-height: calc(100vh - 200px);
		}
	}

	&.rich {
		.footer {
			background-color: rgba(255,255,255,0.8);
			margin: 0 -1em -1em -1em !important;
			padding: 1.5em;

			.icon-lightbulb:first-child:before {
				text-align: left;
				width: 0.8em;
			}
		}

		h3 {
			font-weight: bold;
		}
	}
}