.pat-message {
  	&.status-sentinel {
  		position: relative;
  		margin-top: 20px;

  		.close-panel {
		    font-size: 18px;
		    display: block;
		    z-index: 9999;
		    width: 100%;
		    color: transparent;
		    background: transparent;
		    position: relative;
		    top: -5px;
			right: -5px;
			opacity: 0.5;

			&:hover {
				opacity: 1;
			}

		    &:after {
		      color: black;
		      font-family: fontello;
		      content: $glyph-close;
		      z-index: 1;
		      float: right;
		      padding: 0.3em;
		    }
		}

  		&:before,
  		&:after {
  			content: ' ';
		    display: block;
		    top: -17px;
		    right: 12px;
		    position: absolute;
		    z-index: 0;

			  width: 0; 
			  height: 0; 
			  border-left: 17px solid transparent;
			  border-right: 17px solid transparent;
			  border-bottom: 17px solid #f4f0c2;
  		}

  		&:after {
  			top: -13px;
		    right: 16px;
  			z-index: 1;
			  border-left: 13px solid transparent;
			  border-right: 13px solid transparent;
			  border-bottom: 13px solid $colour-notice;
  		}
  	}
}