.certificate-overview {
	display: grid;
	gap: 35px;

	.certificate-preview {
		overflow: hidden;

		* {
			cursor: pointer;
		}

		figcaption {
			margin-top: 5px;
			display: block;
			font-weight: 20px;
			text-align: center;
			cursor: default;
		}
	}

	@include screen-size(large) {
		grid-template-columns: 1fr 1fr;
	}

	@include screen-size(medium) {
		grid-template-columns: 1fr 1fr;
	}

	@include screen-size(small) {
		grid-template-columns: 1fr;
	}
}