$colour-tree-section-active: #efefef !default;

#steps {

	> li {
		display: block;
		position: relative;
		// border-bottom: solid silver 1px;
		border-radius: $border-radii;

		&:not(.active):hover {
			> a {
				color: black;
				text-decoration: underline;
			}
		}

		&.injecting {
			> * {
				opacity: 1;
			}

			&:before {
				display: none;
			}
		}

		&.disabled {
			opacity: 0.3;
		}
		
		&.active {
			background: $colour-tree-section-active;
			// border-radius: 10px;
			border-left: 5px solid $colour-accent;
			margin-left: -5px;

			> a {
				// padding-left: 2.3rem;
			}
		}

		// &.active:not(#status) {
		// 	background: $colour-tree-section-active;
		// 	// border-radius: 10px;
		// 	border-left: 5px solid $colour-accent;
		// 	margin-left: -5px;

		// 	> a {
		// 		// padding-left: 2.3rem;
		// 	}
		// }
		
		// &.active#status {

		// 	> a {
		// 		background: $colour-tree-section-active;
		// 		// border-radius: 10px;
		// 		border-left: 5px solid $colour-accent;
		// 		margin-left: -5px;
		// 	}
		// }

		> a:not(.onscreen-help) {
			padding: 0.8em 22px 0.8em 3rem;
			display: block;
			font-weight: bold;
			position: relative;

			.pie-graph {
				margin-left: 10px;
				top: 2px;
				float: right;
				// box-shadow: inset 0 0 4px rgba(0,0,0,0.6);
			}

			.discrete {
				opacity: 0.5;
				font-size: 0.8em;
				font-style: normal;
			}		

			&:active {
				// outline: silver solid 1px;
				color: black;
			}

			&:before {
				font-family: fontello;
				font-size: 0.9em;
				content: "";
				float: left;
				text-align: center;
				font-weight: normal;
				position: absolute;
				top: 50%;
				left: 1.4rem;
				transform: translateY(-50%);
				width: 1rem;
			}

			&.preparation:before {
				content: '#{$glyph-home}';
			}

			&.identification:before {
				content: '#{$glyph-ok-circle}';
			}

			&.action-plan:before {
				content: '#{$glyph-icon-edit}';
			}

			&.consultancy:before {
				content: '#{$glyph-lightbulb}';
				font-size: 1.1em;
			}

			&.report:before {
				content: '#{$glyph-document-inv}';
				font-size: 0.8em;
			}

			&.status:before {
				content: '#{$glyph-chart-line-1}';
				font-size: 0.8em;
			}

			&.home:before {
				content: '#{$glyph-cancel}';
				font-size: 19px;
			}

			&.settings:before {
				content: '#{$glyph-cog}';
			}

			&.help:before {
				content: '\e802';
			}

			&.training:before {
				content: '\e84c';
			}

			&.involve:before {
				content: '#{$glyph-icon-chat-empty}';
			}
		}

		.location-tag {
			padding-left: 3rem;
			padding-top: 0.5em;
		}

		&#status {
			margin-top: 2em;
			// position: relative;

			// &:before {
			// 	position: absolute;
			// 	top: -0.95em;
			// 	background-color: gray;
			// 	right: 6%;
			// 	left: 6%;
			// 	content: ' ';
			// 	display: block;
			// 	height: 1px;
			// 	opacity: 0.5;
			// }
		}
	}

	.topics {
		overflow: auto;
		font-size: 16px;

		&.injecting {
			> * {
				opacity: 1;
			}

			&:before {
				display: none;
			}
		}

		> ol {
			margin-bottom: 2em;

			ol:last-child {
				margin-bottom: 1em;
			}
		}

		.module-title {
			font-size: 1em;
			color: rgba(0,0,0,0.5);
			margin-bottom: 0.8em;
			margin-left: 2em;

			@include screen-size(large) {
				display: none;
			}
			
		}

		li {
			position: relative;

			&.current {
				> a {
					background-color: rgba(0,0,0,0.1);
					// border-radius: 2em;
				}
			}

			a {
				padding: 0.4em 6% 0.4em 3rem;
				display: block;
				font-weight: normal;
			}

			&.submodule {
				// > a {
				// 	padding-left: 3.2rem;
				// }

				// &:before {
				// 	content: ' ';
				// 	border-left: solid 2px gray;
				// 	position: absolute;
				// 	top: 0.65rem;
				// 	bottom: 0.65rem;
				// 	left: 2.2rem;
				// }
			}

			li a {
				// padding-left: 3.2rem;
				font-weight: normal;
			}

			.counter {
				position: absolute;
				font-size: 0.7em;
				left: 0;
				color: rgba(0,0,0,0.5);
				width: 2.4rem;
				display: block;
				text-align: right;
				line-height: 26px;
			}
		}


		.questions {
			li {
				line-height: 1.3em;
				font-size: 0.8em;
				margin-bottom: 0;

				a {

					padding-left: 4.7rem;

					&:before {
						left: 3rem;
						top: 50%;
						margin-top: -0.6rem;
					}
				}
			}
		}

		a {
			color: black;			
		}

		.legend {
			font-size: 18px;
			padding: 1em 0 0 0;
			border-top: 1px solid silver;
			margin-bottom: 1.5rem;
			margin-left: 2.15rem;
			// background-color: white;

			li {
				position: relative;
				padding-left: 1.5rem;
				color: gray;
				padding-right: 0.5em;
				display: inline-block;

				&:before {
					font-family: fontello;
					content: "#{$glyph-angle-circled-right}";
					color: white;
					background: gray;
					position: absolute;
					left: 0rem;
					border-radius: 50%;
					padding: 2px;
					top: 50%;
					margin-top: -0.67em;
					display: block;
					height: 18px;
					width: 18px;
					text-align: center;
					line-height: 19px;
					transform: scale(0.65);
				}

				&.postponed {
					&:before {
						content: "#{$glyph-help-circle}";
						background: orange;
					}
				}

				&.answered,
				&.no-risk {
					&:before {
						content: "#{$glyph-ok-circle}";
						background: green;
					}
				}

				&.answered.risk {
					&:before {
						content: "#{$glyph-cancel-circled}";
						background: red;
					}
				}

				&.answered.risk.measures {
					&:before {
						content: "\e8ae";
						background: DarkRed;
					}
				}
			}
		}
	}
}

.osc-off #steps > li .topics .module-title {
	display: block;
}

@include screen-size(small) {
	.focus-document {

		#steps {

			.topics {
				display: none;
			}
		}
	}
}

@include screen-size(medium) {
	.osc-s-on #steps .topics {
		opacity: 0.5;
		pointer-events: none;
	}
}

@include screen-size(small) {
	.osc-s-on #steps .topics {
		opacity: 0.5;
		pointer-events: none;
	}
}