.published-certificate {
	width: 100%;

	.certificate-view,
	.certificate-data {
		box-sizing: border-box;
		float: left;
		padding-bottom: 40px;
	}

	.certificate-view {
		@include screen-size(small) {
			width: 100%;
		}
		@include screen-size(medium-large) {
			width: 40%;
		}
	}

	.certificate-data {
		@include screen-size(small) {
			width: 100%;
		}
		@include screen-size(medium-large) {
			width: 60%;
			padding-left: 6%;
		}
	}
}