ul.questions,
ol.questions {

	li {
		margin-bottom: 0.2em;

		a {
			padding: 0em 0 0 30px;
			font-weight: normal;
			position: relative;
			display: block;

			&:before {
				font-family: fontello;
				content: "#{$glyph-angle-circled-right}";
				color: white;
				background: gray;
				position: absolute;
				left: 0;
				border-radius: 50%;
				padding: 2px;
				display: block;
				height: 14px;
				width: 14px;
				text-align: center;
				line-height: 15px;
				font-size: 14px;
			}
		}

		&.postponed {
			a:before {
				content: "#{$glyph-help-circle}";
				background: orange;
			}
		}

		&.answered {
			a:before {
				content: "#{$glyph-ok-circle}";
				background: green;
			}
		}

		&.answered.risk {
			a:before {
				content: "#{$glyph-cancel-circled}";
				background: red;
			}
		}

		&.answered.risk.measures {
			a:before {
				content: "\e8ae";
				background: DarkRed;
			}
		}
	}
}