$advanced-search-animation-speed: 0s;
$container-padding: 15px;
$colour-search-box: black;
$colour-search-box-text: black;
$advanced-search-field-height: 36px;

.advanced-search {
  overflow-x: hidden;
  overflow-y: visible;
  padding: 0 10%;
  margin-bottom: 0;
  transition: margin-bottom 0.4s !important; 

  &.open,
  &.closed.in-progress {
    margin-bottom: 40px;

    .panel-content {
      padding: 40px
    }
  }

  .panel-content {
    padding: 40px;
    background-color: white;
    border-radius: $border-radii;
    background: rgba($color: #000000, $alpha: 0.03);
    box-shadow: inset 0 1px 6px rgba(0, 0, 0, 0.15);
    transition: $advanced-search-animation-speed;
  }

  .search-rule {
    margin: 0;
    // overflow-x: hidden;
    // overflow-y: visible;
    transition: all $advanced-search-animation-speed;
    clear: both;
    position: relative;
    display: grid;
    grid-template-columns: 30% auto 30px;
    grid-template-areas: 
      "and-or and-or and-or"
      "selector parameters functions";

    &.row-1 {
      // height: 2em;
    }

    .view-compact & {
      display: none;
    }

    time,
    select,
    input {
      @include box-sizing;
      margin: 0;
      height: $advanced-search-field-height;
      line-height: $advanced-search-field-height;
      padding: 0 0.5em;
      width: 100%;
      float: left;
      color: black;
      font-weight: normal;
      border-top-right-radius: $border-radii;
      border-bottom-right-radius: $border-radii;
    }

    .and-or.pat-select:after {
      .header.home & {
        background-image: url(data:image/svg+xml;base64,);
        color: white;
      }
    }

    [data-option-value="lacks"] select {
      background-image: url(data:image/svg+xml;base64,);
      color: white;
    }

    .and-or.pat-select.bare {
      width: 100%;
      // text-align: center;
      margin: 1em auto;
      display: inline-block;
      padding: 0 15px;
      color: black;
      font-size: 0.8em;
      grid-area: and-or;

      .header.home & {
        color: white;

        &:after {
          color: white;
        }
      }

      &:after {  
        right: auto !important;
      }

      select {
        width: auto;
        right: inherit;
        color: black;

        .header.home & {
          color: white;
        }
      }
    }

    .field-choice {
      width: 30%;
      overflow: hidden;
      transition: all $advanced-search-animation-speed;
      grid-area: selector;

      &.pat-select {
        @include select-box;
        width: calc(100% - 20px);
        height: $advanced-search-field-height;
        // line-height: $advanced-search-field-height;
      }

      select {
        width: 93%;
      }

      &[data-option-value="SearchableText"] ~ .parameters.lemma,
      &[data-option-value="searchable-text"] ~ .parameters.lemma,
      &[data-option-value="Title"] ~ .parameters.lemma,
      &[data-option-value="title"] ~ .parameters.lemma,
      &[data-option-value="content"] ~ .parameters.lemma,
      &[data-option-value="Description"] ~ .parameters.lemma,
      &[data-option-value="description"] ~ .parameters.lemma,
      &[data-option-value="author"] ~ .parameters.lemma,
      &[data-option-value="sector"] ~ .parameters.lemma,
      &[data-option-value="contractor"] ~ .parameters.lemma,
      &[data-option-value="plant"] ~ .parameters.plant,
      &[data-option-value="created"] ~ .parameters.date,
      &[data-option-value="effective"] ~ .parameters.date,
      &[data-option-value="modified"] ~ .parameters.date,
      &[data-option-value="start_date"] ~ .parameters.date,
      &[data-option-value="end_date"] ~ .parameters.date,
      &[data-option-value="operator_id"] ~ .parameters.department,
      &[data-option-value="group_id"] ~ .parameters.department,
      &[data-option-value="department"] ~ .parameters.department,
      &[data-option-value="supplier_representative"] ~ .parameters.supplier_representative,
      &[data-option-value="daimler_representative_id"] ~ .parameters.supplier_representative {
        display: flex;
      }
    }

    .parameters {
      // width: calc(70% - 3em);
      transition: width $advanced-search-animation-speed;
      // float: left;
      position: relative;
      display: none;
      grid-area: parameters;
      height: 36px;

      [data-option-value="contains"],
      [data-option-value="ilike"] {
        background-color: lightgreen !important;
      }

      [data-option-value="contains-not"],
      [data-option-value="notilike"] {
        background-color: orange !important;
      }

      [data-option-value="#{$form-param-equals}"],
      [data-option-value="#{$form-param-is}"],
      [data-option-value="__eq__"] {
        background-color: lightblue !important;
      }

      > .pat-select {
        float: left;
        width: 30%;
        margin: 0;
        @include box-sizing;
        @include select-box;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;

        &:after {
          border-top-right-radius: 0px;
          border-bottom-right-radius: 0px;
        }

        transition: all $advanced-search-animation-speed;
      }

      input,
      .select2-container,
      .output-field {
        width: 70%;
        border: none;
        padding: 0 0.8em;
        margin: 0;
        border: none;
        z-index: 4;
        box-shadow: none;
        transition: all $advanced-search-animation-speed;
        position: absolute;
        top: 0;
        left: 30%;
        @include box-sizing;
        color: black;
        background-color: white;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        // height: 36px;
      }

      .select2-container {
        padding: 0;
        border-top-right-radius: $border-radii;
        border-bottom-right-radius: $border-radii;
        height: 100%;

        .select2-choice {
          padding: 0 0.8em;
          line-height: $advanced-search-field-height;
          background-color: transparent;
          // border-top-left-radius: 0;
          // border-bottom-left-radius: 0;
          position: static;
          float: left;
          box-shadow: none !important; // Prevents an unwnated white line to appear above the searchbox when opened.
        }
      }
    }

    .remove-clone {
      border: none;
      background: none;
      width: 1.5em;
      height: 2em;
      overflow: hidden;
      margin: 0;
      padding: 0;
      right: 15px;
      text-indent: -1000px;
      top: 48px;
      grid-area: functions;

      &:hover:before {
        color: black;
      }

      &:active {
        box-shadow: none !important;
        outline: none;

        &:before {
          color: $colour-accent;
        }
      }

      &:before {
        font-family: fontello;
        content: '#{$glyph-remove}';
        color: rgba(0,0,0,0.5);
        position: absolute;
        right: 0;
        text-indent: -1em;

        .header.home & {
          color: white;
        }
      }
    }
  }

  .button-bar {
    overflow: hidden;
    transition: all $advanced-search-animation-speed;
    height: 35px;
    margin: 30px 0 0 0;

    .header.home .view-extended & {
      margin-bottom: 2em;
    }

    .view-compact & {
      height: 0;
      margin-bottom: 0;
      opacity: 0;
    }

    // .pat-button.icon-search {
    //   color: white;
    // }
  }
}
