.post {
  .comments,
  .update-social,
  .update-social .button-bar {
    border-bottom-right-radius: 0.95 * $border-radii;
    border-bottom-left-radius: 0.95 * $border-radii;
  }

  .comments {
    > div {
      overflow: hidden;
      z-index: 1;
      position: relative;
    }
  }
}

.comments {

  margin: 0 0 1em 0;
  // position: relative;
  font-size: 0.9em;
  // padding: 1em;
  position: relative;

  .comment {    
    .update-social {
      margin: 0 0 10px 0;
      overflow: hidden;

      &.status-inactive {
        margin: 0 !important;
        height: 0;
      }
    }
  }

  .document-listing & {
    color: gray;
    // background-color: white;
    position: relative;
    font-size: 0.8em;
  }

  .post & {
      margin: 0 -1em -1em;
      clear: both;

      .older-comments {
        position: relative;
        padding: 0.8em 1em 0.5em 3.5em;
        margin: 0;

        a {
          &:before {
            content: '#{$glyph-arrow-up-circle} ';
            font-family: fontello;
          }
        }

        .counter {
          float: right;
          opacity: 0.5;
        }
      }
  }

  .slideshow-running & {
    display: none;
  }

  .comment-trail > .comment:first-child {
    border-top: 1em solid transparent;
  }

  .comment:last-child {
    // padding-bottom: 2em;

    .post & {
      // border-bottom: 1px solid rgba(0,0,0,0.2);
    }
  }

  .document-comments-header {
    padding: 0 0 0 3.5em;
    position: relative;
    margin-bottom: 0.3em;
    clear: both;

    br {
      display: none;
    }

    .pat-avatar {
      position: absolute;
      left: 0;
      width: 2.8em;
      height: 2.8em;
    }

    .byline { 
      color: gray;
      margin-bottom: 0.5em;
    }
  } 

  .update-comments {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $colour-comment-trail;
    border-radius: $border-radii;
    color: transparent;
    display: none;
  }

  > .update-comments {
    &.has-comments {
      display: block;
    }
  }

  &#comments-document-comments,
  &#comments-news-comments {
    @include screen-size(large) {
      padding: 30px 40px;
    }

    @include screen-size(medium) {
      padding: 30px 40px;
    }

    @include screen-size(small) {
      padding: 20px 0;
    }
    
    // background-color: $colour-base-light;

    .update-social {
      @include screen-size(medium-large) {
        width: auto;
        margin: 0.5em 1em 3.5em 3.5em;
      }

      @include screen-size(small) {
        margin: 0.5em 1em 2em 3.5em;
      }
    }

    @media print {
      // padding: 6% 0;
      // background-color: transparent;
      display: none;
    }
  }
}

