@charset "UTF-8";

$colour-data-matrix-icons: rgba(0,0,0,0.6);
$colour-data-matrix-icons-hover: black;

ul.data-matrix {
	margin-left: 0;
	margin-bottom: 30px;
	display: table;
	width: 100%;
	box-sizing: border-box;

	&.hd {
		font-size: 0.8em;
	}
	
	@include screen-size(medium-large) {
		&.framed {
			background: white;
			padding: 30px;
			border: 1px solid $colour-accent;
			border-radius: border-radii;
		}
	}

	.row {	
		line-height: 1.4;
		padding-right: 60px;
		position: relative;
		
		@include screen-size(medium-large) {
			display: table-row;
		}
		
		@include screen-size(small) {
			display: block;
			margin-bottom: 20px;
			padding: 15px 60px 15px 15px;
			background: white;
			box-shadow: $default-box-shadow;
			border-radius: $border-radii;
		}

		&.archived {
			.field {
				&.name {	

					.follow {
						color: black;
						opacity: 0.5;

						&:before {
							color: black;
						}

						&:after {
							color: black;
							font-family: fontello;
							content: '#{$glyph-archived}';
							font-size: 0.8em;
							vertical-align: middle;
							padding-left: 0.5em;
							position: relative;
							top: -0.1em;
						}
					}
				}
			}
		}



		.field {
			padding-top: 0.2em;
			padding-bottom: 0.2em;

			@include screen-size(medium-large) {
				display: table-cell;
				vertical-align: middle;
				padding-left: 0.5rem;
				padding-right: 0.5rem;
				display: table-cell;
			}

			@include screen-size(small) {
				position: relative;
			}

			&:not(.icons):not(.name) {
				.span-1 & {
					display: block;
					padding: 0;
				}
			}

			.last-saved {
				clear: both;
			}

			&.name,
			&.user,
			&.tool,
			&.probe {

				a,
				.value {
					padding-left: 32px;
					display: block;
					position: relative;
					line-height: 1.2;

					&:before {
						font-family: fontello;
						display: inline-block;
						transform: translateY(-50%);
						top: 50%;
						position: absolute;
						left: 0;
						width: 32px;
						text-align: center;
						margin: 0;
					}

					&[href]:hover {
						text-decoration: underline;
					}
				}
			}

			&.name {
				padding-left: 0;
				a {
					&:before {
						content: '#{$glyph-risk-assessment}';
					}
				}
			}

			&.user {
				padding-left: 0;
				a {
					&:before {
						content: '#{$glyph-icon-user}';
					}
				}
			}

			&.tool {
				a {
					&:before {
						content: '#{$glyph-oira-tool}';
					}
				}
			}

			&.icons {
				width: 0;
				text-align: right;
				padding-right: 0;
				padding: 0;

				@include screen-size(medium-large) {
					padding-left: 0;
					padding-left: 20px;
					white-space: nowrap;
				}

				$data-matrix-icon-size: 24px;
				$data-matrix-icon-padding: 8px;

				.icon {
					display: inline-block;
					vertical-align: middle;
					width: $data-matrix-icon-size;
					height: $data-matrix-icon-size;
					line-height: $data-matrix-icon-size;
					padding: $data-matrix-icon-padding;
					margin-top: -3px;
					margin-bottom: -3px;
					color: transparent;
					overflow: hidden;
					position: relative;
					border-radius: $border-radii;
					cursor: default;

					&:before {
						font-family: fontello;
						display: block !important;
						position: absolute;
						top: $data-matrix-icon-padding;
						left: $data-matrix-icon-padding;
						height: $data-matrix-icon-size;
						line-height: $data-matrix-icon-size;
						width: $data-matrix-icon-size;
						text-align: center;
						color: black;
						font-size: 20px;
					}

					.pie-graph {
						--pie-graph-segment-colour: rgb(160,160,160);
						&:before,
						&:after {
							--pie-graph-segment-colour: rgb(160,160,160);
						}

						border-color: $colour-data-matrix-icons;
					}

					&:hover {
						.pie-graph {
							&:before,
							&:after {
								--pie-graph-segment-colour: black !important;
							}

							border-color: black !important;
						}
					}

					&.tooltip-active-click {
						background-color: $colour-accent;

						&:before {
							color: white !important;
						}

						.pie-graph {
							border: 2px white solid !important;

							&:before,
							&:after {
								--pie-graph-segment-colour: #{$colour-accent} !important;
							}
						}
					}

					&[href] {
						&:before {
							color: $colour-data-matrix-icons;
						}

						&:hover {								
							&:before {
								color: $colour-data-matrix-icons-hover;
							}
						}
					}
					
					&.locked {
						&:before {
							content: '#{$glyph-icon-lock}'
						}
					}
					
					&.validated {
						&:before {
							content: '#{$glyph-icon-lock}';
							color: $colour-success;
						}
					
						&.tooltip-active-click {
							background-color: $colour-success-background;
					
							&:before {
								color: $colour-success !important;
							}
						}
					}
					
					&.under-review {
						&:before {
							color: $colour-warning;
						}
					
						&.tooltip-active-click {
							background-color: $colour-warning-background;
					
							&:before {
								color: $colour-warning !important;
							}
						}
					}

					&.unlocked {
						&:before {
							content: '#{$glyph-icon-lock-open}'
						}
					}

					@include screen-size(medium-large) {
						margin-left: 5px;
					}

					@include screen-size(small) {
						margin-right: 5px;
					}
				}
			}

			&.status,
			&.icons {
				@include screen-size(small) {
					// padding-left: 32px;
				}
			}

			&.icons {
				@include screen-size(small) {
					position: absolute;
					top: 14px;
					right: 14px;
					width: 40px;
				}
			}

			&.date,
			&.status,
			&.updated,
			&.probe {
				color: #666;

				.span-1 & {
					padding-left: 1.5rem;
				}

				// @include screen-size(small) {
				// 	padding-left: 1.5rem;
				// }

				@include screen-size(medium-large) {
					// text-align: right;
					// white-space: nowrap;

					.span-1 & {
						text-align: left;
						white-space: inherit;
						padding-bottom: 0.4em;
					}

					.published {
						background: #efefef;
						border-radius: 4px;
						display: inline-block;
						padding: 0 0.4em;
						margin-right: 15px;

						.span-1 & {
							background: transparent;
							display: inline;
							padding: 0;
							margin: 0;
						}
					}
				}
			}
		}		
	}
}
