.pat-button {
	font-style: normal;

	// &.default,
	// &.default:hover {
	// 	background-color: $colour-secondary;
	// 	color: white;
	// }

	&.icon-desktop {
		&:before {
			margin-right: 0.4em;
			margin-left: -0.05em;
		}
	}
	
	&.facebook {
		color: white;
		background-color: #3b5998;

		&:hover {
			color: white !important;
			filter: brightness(1.1);
		}

		&:before {
			color: white;
		}
	}

	&.twitter {
		color: white;
		background-color: #00aced;

		&:hover {
			color: white !important;
			filter: brightness(1.1);
		}

		&:before {
			color: white;
		}
	}

	&.email {
		color: black;
		background-color: white;
	}

	&:focus {
		border: none;
		box-shadow: 0 0 10px $colour-accent;
		outline: 1px solid white;
	}

