.panel-browser-warning {
	.no-js & {
		h1,
		.button-bar {
			display: none;
		}
	}
}

.browser-warning {
	// color: red;
	margin: 2px 0;

	.no-js & {
		background: white;
		padding: 60px 60px 15px 60px;
		border-radius: $border-radii;
	    box-shadow: 0 2px 50px rgba(0,0,0,0.7);
	    border: 1px solid rgba(0,0,0,0.15);
	    width: 800px;
	}

	.message {
		margin-bottom: 45px;

		&.with-js {
			.no-js & {
				display: none;
			}
		}

		&.without-js {
			.js & {
				display: none;
			}
		}
	}

	.browser-options {
		margin-bottom: 50px;
		text-align: center;
		font-size: 26px;
		line-height: 44px;

		a {
			display: inline-block;
			margin: 0 10px;
			text-decoration: none;
			padding: 10px 15px;
			// background-color: $button-background-colour-subdued;
			line-height: 50px;
			border-radius: $border-radii;
			// font-weight: bold;
			color: $colour-link;

			&:hover {
				// background-color: $button-background-colour;
				color: black;
				text-decoration: underline;
			}

			&:before {
				text-decoration: none;
				font-weight: normal;
			}
		}
	}
}
