#osc {
	background: $colour-sidebar;
	color: $colour-sidebar-text;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	padding-bottom: 2.5%;
	z-index: 11;

	@media print {
		display: none;
	}

	-moz-transition: all 0.5s, filter 0.2s;
	-webkit-transition: all 0.5s, filter 0.2s;
	transition: all 0.5s, filter 0.2s;

	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;

	a {
		color: $colour-sidebar-links;
	}

	&.injecting,
	.injecting {
		&:before {
			display: none;
		}

		> * {
			opacity: initial;
		}
	}

	#organisation-logo {
		padding: 4% 4%;
		text-align: center;
		position: absolute;
		left: 6%;
		bottom: 4%;
		right: 6%;
		background: white;
		border-radius: 0.8em;
		transition: transform 1s;
		animation: fadeInUp 1s;

		img {
			max-width: 90%;
			max-height: 100px;
		}

		@media screen and (max-height: 510px) {
			display: none;
		}

		&.native {
			background: none transparent;
			opacity: 0.5;

			&:hover {
				opacity: 1;
			}

			figure {
				width: 110px;
				margin: 0 auto;
				display: block;

				figcaption {
					color: white;
					font-size: 0.8em;
					line-height: 1.2em;
				}

				img {
					max-height: 1000px;
					width: 100%;
					max-width: 100%;
				}
			}
		}
	}

	#miller-columns ~ #organisation-logo {
		// transform: translateY(100%);
	}

	@media only screen and (min-width: 780px) {
		&:after {
			background: linear-gradient(to right, rgba(0,0,0,0) 0%, black 100%);
			content: ' ';
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 10px;
			z-index: 100;
			opacity: 0.15;
		}
	}

	#osc-header,
	.osc-header {
		padding: $sidebar-top-padding $sidebar-left-padding 28px $sidebar-left-padding;
		margin-bottom: 0;
		height: 26px;
		position: relative;
		z-index: 2;
		border: none;
		background: linear-gradient(to bottom, rgba(230,230,230,1) 0%,rgba(230,230,230,1) 60%,rgba(230,230,230,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

		#button-sessions {
			max-width: 70%;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			cursor: default;
		}

		.column-title {
			padding: 0 50px;
			font-size: 16px;
			font-weight: bold;
			margin: 0;
			text-align: center;
			display: block;
			color: $colour-sidebar-text;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}

		.back-button,
		.pat-select {
			position: absolute;
			top: $sidebar-top-padding;
			left: $sidebar-left-padding;
		}

		.back-button {
			overflow: hidden;
			width: 30px;
			white-space: nowrap;
			margin-top: -3px;
			font-size: 25px;

			&:before {
				content: $glyph-angle-left;
				font-family: fontello;
				display: inline-block;
				padding-right: 30px;
			}
		}

		.icon-cog,
		#button-new-session-quick,
		#button-osc-expand,
		.pat-context-menu {
			float: right;
			line-height: 1.5em;
			margin-left: 0.5em;
			position: absolute;
			top: $sidebar-top-padding;
			right: $sidebar-right-padding;
			white-space: nowrap;
			height: 18px;
			cursor: pointer;
			background: transparent;

			&:before {
				margin: 0;
				display: block;
				text-align: right;
				line-height: 19px;
			}
		}

		.pat-context-menu {
			white-space: normal;

			.context-menu-label {
				line-height: 24px;
				height: 24px;
				width: 30px;
				padding: 0;

				&.collapsible-open {
					color: $colour-accent;
				}

				&:before {
					content: '#{$glyph-ellipsis-vertical}';
					line-height: 20px;
					height: 20px;
					display: block;
					transform: none;
					font-family: fontello;
					text-align: center;
					margin: 0;
				}
			}
		}

		#button-osc-expand {
			right: calc(#{$sidebar-right-padding} + 30px);

			.size-full & {
				color: $colour-accent;
			}
		}
	}

	.search-form {	
		padding: 0 $sidebar-left-padding 0 $sidebar-right-padding;
		font-size: 18px;
		margin-bottom: 0;
		transition: margin 0.4s;

		&.view-extended {
			// margin-bottom: 40px;
		}

		.quick-search {
			width: 100%;
			color: black;
			background: white;
			// font-size: 18px;
		}
	}

	.messages-area {	
		padding: 0 $sidebar-left-padding 20px $sidebar-right-padding;
	}

	.miller-columns {
		font-size: 14px;
		position: absolute;
		z-index: 0;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		padding-top: 85px;
		overflow: auto;
		z-index: 1;
		animation: fadeIn 0.4s;

		.browser {
			animation: fadeIn 0.2s;
			animation-fill-mode: forwards;
			padding: 0 $sidebar-left-padding 2em;

			.item {
				margin: 0;
				line-height: 20px;
				padding: 0;
				position: relative;
				border-bottom: 1px solid silver;

				&.group + .object {
					margin-top: 20px;
					border-top: 1px solid silver;
				}

				@include clear;

				.more-menu {
					position: absolute;
					right: 0;
					top: 50%;
					right: 8px;
					transform: translateY(-50%);
					overflow: hidden;
					color: transparent;

					&:before {
						color: black;
					}
				}

				&:first-child {
					border-top: 1px solid silver;
				}

				&.archived {
					.object-name {
				        .ra-name:after {
				        	font-family: fontello;
				        	content: '#{$glyph-archived}';
				        	color: gray;
				        	display: inline-block;
				        	padding-left: 0.5em;
				        	font-size: 0.7em;
							position: relative;
							top: -0.1em;
				        }
					}
				}

				.object-name {
					font-size: 1em;
					position: relative;
					color: $colour-sidebar-text;
					padding: 11px 6% 11px 0;
					display: block;

					.department-name {
						color: $colour-sidebar-text;

						&:before {
							content: '(';
						}

						&:after {
							content: ')';
						}
					}

					&.current {
						background: $colour-accent;

						color: white;
					}
				}

				.field {
					.osc-size-full & {
						float: left;
						padding: 0.5em 0.5em 0.5em 0;
						box-sizing: border-box;
						width: 25%;
					}
				}

				.meta {
					.osc-size-normal & {
						&:after {
							content: ',';
						}

						&:last-of-type:after {
							content: '.';
						}		
					}

					a {
						color: #666;
						text-decoration: none;

						&:hover {
							text-decoration: underline;
							color: black;
						}
					}
				}

				&.type-department {
					.object-name:after {
						font-family: fontello;
						content: '#{$glyph-right-open}';
						position: absolute;
						right: 10px;
						top: 50%;
						transform: translateY(-50%);
					}

					& + .type-tool {
						margin-top: 1.5em;
					}

					.field {
						width: 100%;
					}
				}

				&.type-risk-assessment {
					.osc-size-normal & {
						padding: 11px 25px 11px 45px;

						&:before {
							width: 45px;
							display: block;
							position: absolute;
							top: 50%;
							left: 0;
							transform: translateY(-50%);
							font-family: fontello;
							content: '#{$glyph-risk-assessment}';
							text-align: center;

							padding-right: 30px;
						}
					}

					.osc-size-full & {
						padding: 0 50px 0 0;
						clear: both;
					}
					
					.object-name {

						.osc-size-normal & {
							display: block;
							position: relative;
							padding: 0;
						}

						font-weight: bold;

						a {
							color: $colour-accent;
							display: block;
							text-decoration: none;

							&:hover {
								text-decoration: underline;

								.department-name {
									text-decoration: none;
								}
							}
						}
					}

					.icons {
						color: #666;
						position: absolute;
						top: 50%;
						right: 2px;
						transform: translateY(-50%);
						width: 50px;

						&:hover,
						&.active {
							color: black;
						}
					}
				}

				&.type-tool {
					.object-name {
						padding-left: 45px;
						position: relative;

						&:before {
							width: 45px;
							display: block;
							position: absolute;
							top: 50%;
							left: 0;
							transform: translateY(-50%);
							font-family: fontello;
							content: '#{$glyph-tool}';
							text-align: center;

							padding-right: 30px;
						}

						.counter {
							color: white;
							background: $colour-accent;
							font-size: 0.8em;
							border-radius: 50%;
							min-width: 1.3em;
							line-height: 1.4;
							padding: 0 0.3em;
							box-sizing: border-box;
							display: inline-block;
							text-align: center;
							position: absolute;
							top: 50%;
							right: 10px;
							transform: translateY(-50%);
						}

						.info {
							display: inline-block;
							position: absolute;
							top: 50%;
							right: 1px;
							transform: translateY(-50%);
						}

						&.current {
							.counter {
								background: white;
								color: $colour-accent;
							}
						}
					}

					.sessions {
						padding-left: 45px;
						margin-bottom: 0.5em;
						// line-height: 1.4;

						.session {
							padding-bottom: 0.5em;

							.icons {
								float: right;
							}

							.session-name {
								color: $colour-accent;

								&:hover {
									text-decoration: underline;
								}

								.department-name {
									text-decoration: none;
								}
							}
						}
					}
				}
			}

			.load-more {
				padding: 3em 0;
				display: block;
				text-align: center;
				position: relative;

				a {
					opacity: 0;
				}

				&:before {
					@include throbber;
				}
			}
		}
	}
}

@include screen-size(small) {
	#osc {
		width: 100%;
		top: 48px;
		// overflow: auto;

		#osc-header .back-button {
			top: 0;
			left: 0;
			color: white;
			height: 50px;
			line-height: 53px;
			position: fixed;
			width: 50px;
			background: $colour-toolbar;

			&:before {
				width: 100%;
				text-align: center;
				margin: 0;
			}
		}
	}

	.osc-s-on #osc {
		left: 0;
	}

	.osc-s-off #osc {
		left: -100%;

		#osc-header .back-button {
			display: none;
		}
	}
}

@include screen-size(medium) {
	#osc {
		width: 40%;
	}

	.osc-size-full #osc {
		width: 100%;
	}

	.osc-s-on #osc {
		left: 0;
	}

	.osc-s-off #osc {
		left: -40%;
	}
}

@include screen-size(large) {
	#osc {
		width: 25%;
	}

	.osc-size-full #osc {
		width: 100%;
	}

	.osc-on #osc,
	.osc-size-full #osc {
		left: 0;
	}

	.osc-off #osc {
		left: -25%;
	}
}
