$colour-tag: lightblue !default;

.tag-cloud {
	@include clear;

	.tag {
		display: inline-block;
		padding: 0 0.8em;
		border-radius: 20px;
		background: $colour-tag;
		font-size: 0.8em;
		margin: 0 10px 10px 0;
		color: rgba(0,0,0,0.8);
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		max-width: 100%;
		box-sizing: border-box;
		float: left;

		&[href]:hover {
			color: black;
			filter: brightness(1.1);
		}
	}
}