@charset "UTF-8";

.link-list-title {
  margin: 0;

  & + .link-list {
    margin-top: 0.4em;
  }
}

ul.links,
.link-list {
  display: block;
  margin-bottom: 2rem;
  line-height: 1.3;

  &.risks {
    font-size: 0.9em;

    li {
      a {
        &:before {
          display: none;
        }

        display: table-row;

        .number,
        .label {
          display: table-cell;
          padding-bottom: 0.7em;
          vertical-align: middle
        }

        .number {
          padding-right: 0.5em;
          white-space: nowrap;
          font-weight: bold;

          &:before {
            font-family: fontello;
            content: "\e80b";
            color: white;
            background: gray;
            border-radius: 50%;
            padding: 2px;
            display: block;
            height: 14px;
            width: 14px;
            text-align: center;
            line-height: 15px;
            font-size: 14px;
            display: inline-block;
            margin-right: 7px;
            top: -1px;
            position: relative;
            font-weight: normal;
          }

          &.postponed {
            a:before {
              content: "\e802";
              background: orange;
            }
          }

          &.answered {
            a:before {
              content: "\e80a";
              background: green;
            }
          }

          &.answered.risk,
          &.no-risk {
            a:before {
              content: "\e805";
              background: red;
            }
          }

          &.answered.risk.measures {
            a:before {
              content: "\e8ae";
              background: DarkRed;
            }
          }

        }

        &:hover {
          text-decoration: none;

          .label {
            text-decoration: underline;
          }
        }
      }
    }
  }

  @include screen-size(large) {
    &.col-2 {
      column-count: 2;
      overflow: hidden;
    }

    &.col-3 {
      column-count: 3;
      overflow: hidden;
    }

    &.col-4 {
      column-count: 4;
      overflow: hidden;
    }
  }

  @include screen-size(medium) {
    &.col-2 {
      column-count: 2;
      overflow: hidden;
    }

    &.col-3 {
      column-count: 3;
      overflow: hidden;
    }

    &.col-4 {
      column-count: 3;
      overflow: hidden;
    }
  }

  li {
    display: block;
    padding: 0;
    break-inside: avoid;

    &:before {
      display: none;
    }

    a {
      display: block;
      padding: 0.3em 0 0.3em 1em;
      position: relative;

      &.archived {
        padding-right: 28px;
        color: gray;
        display: inline-block;

        &:after {
          position: absolute;
          right: 4px;
          top: 50%;
          transform: translateY(-50%);
          font-family: fontello;
          content: '#{$glyph-archived}';
          font-size: 14px;
          color: gray;
        }
      }

      &:hover {
        text-decoration: underline;

        &:before {
          text-decoration: none;
        }
      }

      &:not([class*="icon-"]) {

        &:before {
          position: absolute;
          font-family: fontello;
          content: '#{$glyph-right-open}';
          left: 0;
        }
      }
    }
  }

}