@charset "UTF-8";

.pat-nav-tiles {
	padding: 2em 0;
	
	.item {
		width: 33%;
		color:$colour-ueber-back-button-text;
		float: left;
		margin: 0 0 4% 0;
		text-align: center;
		height: 270px;
		text-decoration: none !important;
		transition: transform 0.20s;
		font-weight: bold;

		.hovermq & {
			&:hover {
    			color: darken($colour-ueber-back-button-text,10%);
				text-decoration: none;

				&:before {
	    			color: darken($colour-ueber-back-button-text,10%);
		    		box-shadow: inset 0 0 0 3px $colour-link;
				}

				// transform: scale(1.01);
			}
		}

		&:before {
			width: 200px;
			height: 200px;
    		background-color: rgba($colour-link, 0.08);
			border-radius: $border-radii;
			box-shadow: inset 0 0 0 3px rgba($colour-link, 0.3);
    		color: $colour-link;
			line-height: 200px;
			display: block;
			margin: 0 auto 15px;
			text-align: center;
			font-size: 100px;
			transition: box-shadow 0.20s;
		}

		@include screen-size(small) {
			height: auto;
			width: 100%;
			float: none;
			display: block;
			background-color: transparent;
			margin-bottom: 0.5em;
			padding-bottom: 1em;
			padding-top: 0em;

			&:before {
				width: 100%;
				height: 130px;
				line-height: 130px;
			}
		}

	}
}