.page-module {
	margin-bottom: 30px;
	@include clear;

	.group-title {
		margin: 0 0 20px 0;
		font-size: 1.625rem;
		// padding-top: 10px;
	}

	.tiptap-container .ProseMirror>p[data-placeholder]:first-child:before {
		min-height: 50px;
	}

	&.type-listing {
		.block {
			margin-bottom: 30px;

			&.pat-collapsible {
				border: 1px rgba(0,0,0,0.1) solid;
				background-color: rgba(0,0,0,0.07);
				padding: 20px;
				border-radius: $border-radii;

				table {
					background-color: white;
				}

				> .collapsible-closed,
				> .collapsible-open {
					margin: 0;
					font-weight: bold;
					font-size: 1em;
					line-height: 1.4;
					position: relative;
					padding-left: 30px;

					&:before {
						top: 50%;
						position: absolute;
						left: 1px;
						height: 22px;
						margin: -13px 0 0 0;
						width: 20px;
						font-size: 20px !important;
					}
				}
			}

			table {
				td {
					&.name {
						width: 40%;
					}

					&.status,
					&.last-updated,
					&.probe {
						width: 20%;
					}
				}
			}
		}
	}
}