#canvas-alert-bar,
.canvas-alert-bar {
  margin: 0;
  transition: 0.8s all;
  padding: 0;

  .pat-message {
    margin: 0 0 40px 0;
    border-radius: $border-radii;
  }

  button,
  .pat-button {
    float: right;
    margin-top: -3px;
    font-size: 0.8em;
    margin-right: 0;
    line-height: 32px;
  }

  @include screen-size(small) {
    display: none;
  }
}