$pie-graph-segment-colour: $colour-accent !default;
$pie-graph-line-colour: $colour-accent !default;
$pie-graph-background-colour: white !default;

.pie-graph {
	--pie-graph-segment-colour: #{$pie-graph-segment-colour};
	--pie-graph-line-colour: #{$pie-graph-line-colour};
	--pie-graph-background-colour: #{$pie-graph-background-colour};

	width: 18px;
	height: 18px;
	display: inline-block;
	border-radius: 50%;
	overflow: hidden;
	background: var(--pie-graph-background-colour);
	border: 2px solid var(--pie-graph-line-colour);
	position: relative;
	color: transparent;
	clip-path: circle(50% at 50% 50%);

	&:before,
	&:after {
		content: ' ';
		height: 100%;
		width: 50%;
		display: none;
		background: var(--pie-graph-segment-colour);
		position: absolute;
		top: 0;
		transform-origin: right center;
	}

	#section-certification & {
		width: 200px;
		height: 200px;
		float: right;
		margin-left: 30px;
		border: 10px solid var(--pie-graph-line-colour);
		// box-shadow: inset 0 0 40px rgba(0,0,0,0.5);
		background: lighten($pie-graph-line-colour, 40%);
	}

	&[data-percentage] {
		&:before,
		&:after {
			display: block;
		}
	}

	&:before {
		z-index: 2;
		left: 0;
		transform: rotate(0.5turn);
	}

	&:after {
		z-index: 1;
		left: 50%;
		transform-origin: left center;
	}

	// Values of 50 and below

	&[data-percentage="1"],
	&[data-percentage="2"],
	&[data-percentage="3"],
