.ra-list {
	margin-bottom: 2rem;

	.ra-list-title {
		font-size: 1.2em;
		margin-bottom: 0.5em;
	}

	.tool-name {
		display: block;
		padding-left: 1rem;
		font-size: 1em;
		padding-top: 0.2em;
		padding-bottom: 0.2em;

		&:before {
			font-family: fontello;
			content: '#{$glyph-tool}';
			display: inline-block;
			margin-right: 0.5em;
		}
	}

	.department {
		padding-left: 1rem;
	}
}

ul.ras {
	font-size: 0.8em;
	margin-left: 0;
	margin-bottom: 0.7em;

	.ra {
		
		line-height: 1.4;
		padding-right: 60px;
		position: relative;

		&.archived {
			.field {
				&.name {				
					// padding-right: 28px;
					color: gray;
					// display: inline-block;

					&:after {
						// position: absolute;
						// right: 4px;
						// top: 50%;
						// transform: translateY(-50%);
						font-family: fontello;
						content: '#{$glyph-archived}';
						// font-size: 14px;
						font-size: 0.6em;
						vertical-align: middle;
						padding-left: 0.5em;
						position: relative;
						top: -0.1em;
						// color: gray;
					}
				}
			}
		}

		.follow {
			
			width: 100%;

			@include screen-size(medium-large) {
				display: table;
			}

			.field {
				padding-top: 0.2em;
				padding-bottom: 0.2em;

				@include screen-size(medium-large) {
					display: table-cell;
					vertical-align: top;
					padding-left: 0.5rem;
					padding-right: 0.5rem;
				}

				.span-1 & {
					display: block;
					padding: 0;
				}

				.last-saved {
					clear: both;
				}

				&.name {
					padding-left: 1.5rem;

					&:before {
						font-family: fontello;
						content: '#{$glyph-risk-assessment}';
						display: inline-block;
						// margin-right: 0.5em;
						margin-left: -1.5rem;
						float: left;
						margin-top: -0.1em;
					}
				}

				&.date {
					color: gray;

					.span-1 & {
						padding-left: 1.5rem;
					}

					@include screen-size(small) {
						padding-left: 1.5rem;
					}

					@include screen-size(medium-large) {
						text-align: right;
						white-space: nowrap;

						.span-1 & {
							text-align: left;
							white-space: inherit;
							padding-bottom: 0.4em;
						}

						.published {
							background: #efefef;
							border-radius: 4px;
							display: inline-block;
							padding: 0 0.4em;
							margin-right: 15px;

							.span-1 & {
								background: transparent;
								display: inline;
								padding: 0;
								margin: 0;
							}
						}
					}
				}
			}
		}

		a.more-menu,
		a.publication-menu {
			color: gray;
			position: absolute;
			top: 0;
			right: 0;

			display: block;
			padding-top: 0.2em;
			padding-bottom: 0.2em;
			line-height: 1.4;

			&:hover {
				color: black;
			}

			&.active {
				color: black;
			}
		}

		a.publication-menu {
			right: 30px;
		}
	}
}