#main {	
	position: absolute;
	left: 25%;
	top: 0;
	right: 0;
	bottom: 0;

	@media print {
		left: 0;
	}

	-moz-transition: all 0.5s, filter 0.2s;
	-webkit-transition: all 0.5s, filter 0.2s;
	transition: all 0.5s, filter 0.2s;

	&.injecting {
		#toolbar,
		#content {
			opacity: 1;

			#main-content {
				opacity: 0.5;
			}
		}
	}
}

@include screen-size(small) {
	#main {
		position: relative;
		left: 0;
		height: 100%;
	}

	.osc-s-on #main {
		// left: 90%;
		// width: 10%;
	}
}

@include screen-size(medium) {
	#main {
		position: relative;
		left: 0;
		height: 100%;
	}

	.osc-s-on #main {
		left: 40%;
	}
}

@include screen-size(large) {
	.osc-on #main {
		left: 25%;
	}

	.osc-off #main {
		left: 0%;
	}
}

