.tool-list {
	line-height: 1.5;

	.legend {
		// margin: 0 !important;
		// font-weight: normal !important;
			font-weight: bold;
			margin-bottom: 0.5em;
	}

	fieldset {
		margin-bottom: 0.5em;
	}

	.tools {
		display: block;
		// width: calc(100% - 3rem);
		margin: 0;

		.tool {
			display: block;
			position: relative;
			border-radius: 0.7em;
			clear: both;
			overflow: hidden;
			padding: 0 0 0 calc(1.5em + 8px);
			margin: 0 0 0 -8px;

			&:before,
			&:after {
				left: 8px !important;
			}

			.field {
				float: left;
				@include box-sizing;

				&.name {	
					// width: 70%;	
					// &:before {
					// 	font-family: fontello;
					// 	content: '#{$glyph-tool}';
					// 	display: inline-block;
					// 	margin-right: 0.5em;
					// }

					font-weight: normal;
				}

				&.date {
					width: 30%;
					color: gray;
					text-align: right;
					padding-right: 15px;
				}
			}

			// &:before {
			// 	display: none;
			// }

			&.checked {		
				color: white;
				background: $colour-accent;
				color: white;

				.date {
					color: rgba(255,255,255,0.5)
				}

				&:after {
					background-color: $colour-accent;
				}

				// &:before {
				// 	content: ' ';
				// 	top: 0;
				// 	right: -10px;
				// 	bottom: 0;
				// 	left: -10px;
				// 	z-index: 0;
				// 	background: $colour-accent;
				// }
			}
		}
	}

	.department {
		.department-name,
		.legend,
		.tools {
			// padding-left: 1rem;
		}

		.department {
			.department-name,
			.legend,
			.tools {
				padding-left: 2rem;
			}

			.department {
				.department-name,
				.legend,
				.tools {
					padding-left: 3rem;
				}
			}
		}
	}
}

