.pat-well {
	clear: both;

	h2:first-child {
		padding-top: 0;
	}

	h1 {
		body & { 
			// Overruling brand style
			font-size: 1.2em;
			font-weight: bold;
			margin-bottom: 0.3em;
		}
	}

	a {
		color: black;
	}

	> *:last-child {
		margin-bottom: 0;
	}

	.pat-collapsible.show-more:last-child:not(.context-menu):not(.pat-modal-pop-over) {
		padding-bottom: 0;
	}

	&.warning {
		border-left: $colour-warning solid 10px;
		background: white;
		// padding-top: 0;
		// padding-bottom: 0;
		color: black;
		// border-radius: 0;
		box-shadow: none;

		select {
			color: inherit;
			border: none;
		    background-image: url(data:image/svg+xml;base64,#{$arrow-down-dark});
		}

		.pat-collapsible .collapsible-closed, 
		.pat-collapsible .collapsible-open {
			// These rules are for the more link on the action plan.

			color: black;

			&:before {
				display: none;
			}
		}

		.pat-collapsible .collapsible-open {
			opacity: 0;
			pointer-events: none;
		}
		
		a,
		.pat-collapsible:not(.context-menu):not(.pat-modal-pop-over) .collapsible-closed,
		.pat-collapsible:not(.context-menu):not(.pat-modal-pop-over) .collapsible-open {
			color: $colour-accent;
		}
	}
}