

.comment-sentinel {
  margin: -1em 0 -1em 0;
  height: 2em;
  text-align: center;
  font-size: 0.8em;
  top: 0 !important;
  white-space: nowrap;

  @media print {
    display: none !important;
  }

  a {
    display: inline-block;
    background-color: var(--colour-accent);
    border: 1px white solid;
    color: white;
    padding: 0 1em;
    // bottom: 0;
    border-radius: 1em;
    position: absolute;
    left: 50%;
    z-index: 1;

    @include screen-size(small) {
      right: -40%;
      width: 90%;
      @include box-sizing;
    }

    transform: translateX(-50%);

    &:hover {
      text-decoration: none;
    }

    &:before,
    &:after {
      font-family: fontello;
      content: '\e871';
      opacity: 0;
      transition: all 0.4s;
    }

    &:before {
      // float: left;
      margin-right: 1em;
    }

    &:after {
      // float: right;
      margin-left: 1em;
    }
  }

  &.bumped-bottom {
    // margin: 0 auto 0;

    a {
      position: fixed;
      bottom: 1em;

      .application-workspace & {
        @include screen-size(medium-large) {
          margin-left: 25px;
        }
      }

      .sidebar-left-open & {
        @include screen-size(large) {
          margin-left: 15%;
        }
      }

      @include screen-size(small) {
        right: -45%;
        @include box-sizing;
      }

      &:hover {
        filter: contrast(140%);
      }

      &:before,
      &:after {
        opacity: 1;
      }
    }
  }
}