@charset "UTF-8";

.search-form {
  @include box-sizing;

  fieldset {
    margin: 0; // Resets default fieldset margins
    position: relative;
  }

  @media print {
    display: none;
  }

  @include screen-size(small) {
    width: 100%;
    margin: 0;
    height: 4.5em;
  }

  @include screen-size(medium-large) {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }

  position: relative;

  .header.search-results & {
    margin: 3em auto 1.2em;
    font-size: 1em;

    .search-box-body {
      box-shadow: none;
      padding: 0;
      background: none;
      margin: 0;

      color: black;

      input {
        background-color: white;
      }
    }
  }

  .toggle-bar {
    padding: 0;
    margin: 15px 0 25px 0;
    position: relative;
    // top: -2.8em;
    transition: all 0.5s;
    font-size: 0.9em;

    @include screen-size(small) {
      display: none !important;
    }

    .header.home & {
      margin-top: -3.4em;
      position: absolute;
      top: inherit;
      bottom: 1.8em;
    }

    .header.home & {
      margin-top: -3.4em;
      position: absolute;
      top: inherit;
      bottom: 1.8em;
    }

    .pat-switch {
      cursor: pointer;
      text-decoration: underline;
      font-style: normal;
      font-weight: normal;
      color: gray;

      &:hover {
        color: black;
      }

      &.checked {
        display: none;
      }

      &:hover {
      }

      input {
        position: absolute;
        opacity: 0;
      }
    }
  }


  &.view-extended .toggle-bar {
    top: 0;
  }

  &.view-extended {
    .search-box-body:not(#search-normal) {

      .header.home & {
        min-height: 12.5em;
      }

      .search-field {
        width: calc(44.47% - 2.8em);
        right: 3.2em;       
        // font-size: 1em;
        opacity: 0;
        pointer-events: none;

        input[type=search] {
          height: 32px;
          width: 100%;
        }

        button {
          font-size: 1em;
          right: 10em;
          height: 32px;
          opacity: 0;
        }
      }
    }
  }

  .search-field {
    position: absolute;
    z-index: 5;
    right: 0;
    width: 100%;

        @include screen-size(small) {
          width: 100%;
          right: 0;
        }
  }

  &.view-compact {
    .-ms- & {
      .search-box-body {
        height: 6em;
      }
    }
  }
}