.training-passess {
	display: grid;
	gap: 40px;
	grid-auto-rows: 1fr;

	.training-pass {
		border-radius: 25px;
		background: white;
		color: #666;
		box-shadow: $default-box-shadow;
		padding: 30px 20px 60px;
		text-align: center;
		display: grid;
		grid-template-rows: auto auto;
		cursor: default;

		// &:hover {
		// 	.button-bar {
		// 		.pat-button {
		// 			transform: scale(1.05);
		// 		}
		// 	}
		// }

		.training-pass-title {
			font-size: 22px;
			display: block;
			text-align: center;
			line-height: 32px;
			margin-bottom: 40px;
			font-weight: normal;
			color: rgba(0, 0, 0, 0.6);

			&:before {
				content: '#{$glyph-training}';
				font-family: fontello;
				font-size: 100px;
				line-height: 190px;
				display: block;
				margin-bottom: 20px;
				text-align: center;
				color: rgba(0, 0, 0, 0.25);
			}
		}

		.button-bar {
			margin: 0;
			text-align: center;
			position: relative;
			overflow: visible;
			align-self: end;

			.pat-button {
				margin: 0;
				float: none;
				transition: 0.15s ease-out;
				cursor: pointer;
				background: transparent;
				color: $colour-accent;
				border: $colour-accent solid 2px;

				&:hover {
					box-shadow:
						0 0 0 2px white,
						0 0 0 4px $colour-accent;
				}
			}
		}
	}

	@include screen-size(large) {
		grid-template-columns: 1fr 1fr 1fr;
	}

	@include screen-size(medium) {
		grid-template-columns: 1fr 1fr;
	}

	@include screen-size(small) {
		grid-template-columns: 1fr;
	}
}