@charset "UTF-8";

.comment {
	position: relative;
	// padding: 0.5em 1em 0.5em 3.5em;
	padding: 0 1em 0.5em 3.5em;
	// margin-bottom: 1em;
	min-height: 2em;

  .comment-reply-box {
    min-height: 0;
    transition: all 1s;

    .update-social {
    	// margin-bottom: 15px;
    }

    @include injection;

    &.injecting {
      min-height: 130px;
      position: relative;

      &:before {
      	margin-top: -45px;
      }
    }
  }

	.comment {
		margin-left: -14px;
		padding-right: 0;
	}

	.pat-avatar {
		position: absolute;
		left: 1em;
		top: 0.3em;
	}

	// &.user-inactive {
	// 	.pat-avatar {
 //          filter: grayscale(100%) brightness(200%) contrast(40%);
	// 	}
	// }

	.comment-footer {
		margin-top: 5px;
		line-height: 1.4;
		font-size: 0.8em;

		form {
			display: inline;
		}

		.button {
			display: inline-block;
			margin-bottom: 10px;
			cursor: pointer;
			color: rgba(black, 0.5);
			margin-right: 10px;
			font-weight: bold;
			background-color: transparent;
			font-size: 1em;

			.counter {
				margin-top: -2px;
				display: inline-block;
			}

			&.active,
			&.executed {
				color: $colour-accent;
			}

			&.executed {
				pointer-events: none;
			}

			&:hover {
				text-decoration: underline;
				color: black;
			}
		}
	}
	
	.comment-content,
	.comment-header {
		font-size: 0.9em;
		line-height: 1.4em;
		@include selectable;

		p,ul,ol,table,dl {
			margin-bottom: 1em;
		}

		*:last-child {
			margin-bottom: 0;
		}
	}

	.comment-header {
		.meta {
			color: gray;

			&:hover {
				text-decoration: none;
			}

			&[href] {
				color: $colour-link;

				&:hover {
					.name {
						text-decoration: underline;
					}
				}
			}
		}
	}

	.date {
		font-style: italic;
		color: gray;
	}

	.document-preview {
		// max-width: 80%;
		// margin: 20px auto;
	}

	.preview {
		margin-top: 5px;

		img {
			max-width: 100%;
			margin: 0 auto;
			display: block;
			background-color: white;
		}
	}

    .edited-toggle {
      transition: opacity 1s;
      opacity: 1;
      cursor: pointer;

      &:hover {
        text-decoration: underline;
      }

      &.collapsible-open {
        opacity: 0;
        cursor: default;
        pointer-events: none;
      }
    }

    .original-text {
       opacity: 0.5;
       font-size: 0.8em;
    }

	.comment-header {
		.name {
			display: inline-block;
			padding-right: 0.15em;

			&:after {
				content: " "
			}
		}

	    .icon-cog,
	    .icon-ellipsis {
      	position: absolute;
      	right: 0;
      	top: 0;
      	color: rgba(0,0,0,0.3);
				font-size: 1em;
				right: 1.5em;
				width: 25px;
				height: 25px;
				line-height: 25px;
      	border-radius: $border-radii;

      	&:before {
					text-align: center;
					width: 100%;
      	}

				&:hover {
					color: black;
				}

	      &.tooltip-active-click {
	        color: white;
	        background-color: var(--colour-accent);
	      }
	    }
	}
}

