$tool-icon-border-radius: 12% !default;

.tool-icon {
	margin-bottom: 20px;
	display: block;
	background: #efefef;
	border-radius: $tool-icon-border-radius;
	box-shadow: $default-box-shadow;
	position: relative;

	&:before {
		color: rgba(0,0,0,0.5);
		font-size: 120px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		font-family: fontello;
		content: '#{$glyph-oira-tool}';
	}

	img	{
		width: 100%;
		background-size: cover;
		background-position: center;
		background-color: white;
		display: block;
		position: relative;
		border-radius: $tool-icon-border-radius;
		object-fit: cover;

		&[style*='url()'] {
			background-color: transparent;
		}
	}

	@include screen-size(small) {
		width: 60%;
		margin: 0 auto 25px;
	}
}