@charset "UTF-8";

$bullet-space: 22px !default;
$menu-list-top-bottom-padding: 3px !default;
$menu-list-line-height: 22px !default;

ul.menu,
.menu-list {
  margin: 0 0 1em 0;
  list-style-type: none;

  // border-top: 1px silver solid;

  li {

    line-height: 2em;
    padding-left: 0;
    position: relative;

    &:not(.current) {
      @include screen-size(medium-large) { 

        &:not(.active):not([disabled]):hover {
          color: black;
          text-decoration: none;
        }
      }
    }

    // border-bottom: 1px silver solid;

    &:before {
      display: none;
    }

    > form.worfklow-state {
      padding: 0.2em 0 0.2em $bullet-space; 
      line-height: 1.3;

      &:before {
        margin-left: -1 * $bullet-space;
        // margin-top: 0.2em;
        float: left;
        line-height: 1.3;
        font-family: fontello;
        content: '#{$glyph-workflow-state}';
        text-align: center;
        width: 26px;
        line-height: 1.5;
      }

      > label.pat-select.bare {
        display: inline-block;
        
        // &[data-value='draft'] {
        //   color: $colour-draft;
        // }

        // &[data-value='public'] {
        //   color: $colour-public !important;
        // }

        // &[data-value='published'] {
        //   color: $colour-published !important;

        //   &:hover {
        //     color: darken($colour-published, 10%) !important;
        //   }
        // }
      }
    }

    a,
    > label:not(.pat-select),
    > form > label:not(.pat-select),
    button {
      display: block;
      // margin-left: -10px;
      // margin-right: -10px;
      // padding-left: 32px;
      // padding-right: 15px;
      padding: $menu-list-top-bottom-padding 0 $menu-list-top-bottom-padding $bullet-space;      
      line-height: $menu-list-line-height;
      // height: 1.5em;
      cursor: default;
      overflow: hidden;
      border: none;
      width: 100%;
      background: none;
      @include box-sizing;
      font-weight: normal;
      font-size: 1em;

      @include screen-size(small) {
        color: black;
      }

      @include screen-size(medium-large) { 
        color: rgba(black, 0.6);
      }

      .pat-message {
        display: block;
        margin-top: 0.4em;
        margin-bottom: 0.5em;
        padding: 0.3em 0.5em;
      }

      &.pat-checklist {
        &:before {
          position: relative;
          left: 0;
          margin-left: -1 * $bullet-space;
          margin-top: 0;
          top: 0.1em;
        }
      }

      input[type="file"] {
        opacity: 0;
        position: absolute;
        left: 0;
        top: 0;
        height: 1.8em;
        width: 100%;
      }

      &.tiptap-heading-level-2 {
        &:before {
          transform: scale(0.8);
        }
      }

      &.tiptap-heading-level-3 {
        &:before {
          transform: scale(0.6);
        }
      }

      &:before {
        margin-left: -1 * $bullet-space;
        // margin-top: $menu-list-top-bottom-padding;
        float: left;
        line-height: $menu-list-line-height;
      }

      > a:hover,
      &:hover {
        text-decoration: none !important;
      }

      &:not(.current) > a:hover,
      &:not(.current):hover {
        color: black !important; // Overrule button hover style.
      }

      &:active {
        box-shadow: none;
      }

      &.active,
      &.active:before {
        color: $colour-accent;
      }

      .directory & {
        cursor: pointer;
      }
    }

    &.current {
      // background-color: white;
      color: $colour-accent;

      a {
        color: $colour-accent;
        cursor: default;
      }
    }
  }
}