.pat-toolbar {
	// z-index: 20; // Keep the toolbar in front of the expandable sidebar on mobile.

	&#training-toolbar {
		.toolbar-navigation-area,
		.toolbar-functions-area {
			grid-template-columns: 3fr 0fr 4fr;
		}
	}
	&#assessment-toolbar {
		.toolbar-navigation-area,
		.toolbar-functions-area {
			@include screen-size(small) {
				grid-template-columns: auto 0fr auto;
			}

			@include screen-size(medium-large) {
				grid-template-columns: auto 0fr auto;
			}
		}
	}

	@include screen-size(small) {
		.editor-toolbar {
			display: none !important;
		}
	}

	@include screen-size(medium-large) {
		#toggle-tree {
			display: none !important;
		}
	}
}