#portal {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgb(147,154,205);
	background: linear-gradient(0deg, rgba(147,154,205,1) 0%, rgba(255,255,255,1) 100%);

	#welcome {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		color: white;
		z-index: 10;
		overflow-x: hidden;
		overflow-y: auto;
		background: url(../../oira/style/portal-curve.svg) repeat-y;
		background-position:  center center;
		transition: left 0.4s;
		// -webkit-pointer-events: none;
		// pointer-events: none;

		@include screen-size(large) {
			background-size: 70% auto;
		}

		@include screen-size(medium) {
			background-size: 90% auto;
		}

		@include screen-size(small-medium) {
			background: $colour-secondary;
		}

		&:before {
			position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			width: 50%;
			background: $colour-secondary;
			content: ' ';
			z-index: -1;
			transition: left 0.4s;

			@include screen-size(small-medium) {
				display: none;
			}
		}

		#intro {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			// display: none;
			animation: fadeIn 0.5s;
			transition: all 0.4s;

			.pat-message {
				margin-top: 40px;
			}

			// &:target { 
			// 	display: block;
			// 	-webkit-pointer-events: auto;
			// 	pointer-events: auto;
			// }

			@include screen-size(large) {
				width: 680px;
			}

			@include screen-size(medium) {
				width: 680px;
			}

			@include screen-size(small) {
				// width: 80%;

				.options {
					height: auto;
					// text-align: center;
					clear: both;

					a {
						margin: 0 auto 25px;
					}
				}
			}

			.title-group {
				h1 {
					color: transparent;
					background: url(oira-logo-dp.svg) no-repeat left bottom;
					background-size: contain;
					width: 300px;
					height: 90px;
					margin-bottom: 15px;
				}

				.byline {
				}
			}

			.options {
				clear: both;
				padding-top: 40px;
				transition: all 0.4s;
				box-sizing: border-box;

				@include screen-size(large) {
					white-space: nowrap;
					height: 82px;
					overflow: hidden;
				}

				a {
					display: inline-block;
					padding: 0 35px;
					line-height: 41px;
					height: 40px;
					border: white;
					border-radius: $border-radii;
					margin-right: 15px;
					border: 1px white solid;
					color: white;

					&:hover {
						background: rgba(255,255,255,0.1);
					}
				}
			}
		}

		#more-information {
			padding: 180px 10%;
			max-width: 680px;
			display: none;
			animation: fadeIn 0.5s;	
			z-index: 2;
			position: relative;

			a {
				color: white;
			}

			.long-read {
				position: relative;
			}

			.ctas {
				margin: 50px 0;

				a {
					font-weight: bold;
				}
			}

			&:before {
				background: $colour-secondary;
				position: absolute;
				top: -180px;
				bottom: -180px;
				left: 10%;
				right: 10%;
				content: ' ';
			}

			&:target { 
				display: block;
				-webkit-pointer-events: auto;
				pointer-events: auto;
				animation: fadeIn 0.5s;	
			}

			.body-text {
				position: relative;
				z-index: 1;
			}

			.pat-toolbar {
				width: 120%;

				@include screen-size(large) {
					margin: -40px -10% 50px;
				}

				@include screen-size(medium) {
					margin: -40px 0 50px;
				}

				@include screen-size(small) {
					margin: -30px 0 30px;
				}

				.pat-back-button {
					// line-height: 39px;
					// color: $back-button-text-colour;

					&:hover {
						// color: $back-button-text-colour-hover;
					}
				}
			}	
		}

	}

	#login,
	#registration {
		position: fixed;
		top: 50%;
		left: 50%;
		right: 0;
		transform: translate(0, -50%);
		z-index: 5;
		display: none;
		animation: fadeIn 0.5s;
		transition: all 0.4s;

		.colofon {
			margin-top: 50px;
		}

		&:target { 
			display: block;

			& ~ #welcome #intro {
				left: 60%;
				width: 320px;

				.options {
					height: 0;
					padding-top: 0;
					opacity: 0;
				}
			}

			& ~ #welcome {
				-webkit-pointer-events: none;
				pointer-events: none;

				.title-group {
					-webkit-pointer-events: auto;
					pointer-events: auto;
				}

				@include screen-size(large) {
					left: -31%;
					right: 31%;
				}
				
				@include screen-size(medium) {
					left: -100%;
					right: 100%;
				}
				
				@include screen-size(small) {
					display: none;
				}
			}

			& ~ #welcome:before {
				@include screen-size(large) {
					left: -31%;
					right: 31%;
				}

				@include screen-size(medium) {
					left: -100%;
					right: 100%;
				}
			}
		}

		.button-bar {
			margin-bottom: 0;
		}

		@include screen-size(large) {
			> fieldset,
			> .pat-rich,
			> .pat-message {
				margin: 0 auto;
				width: 60%;
				max-width: 450px;

				h1 {
					margin-bottom: 30px;
				}
			}
		}

		@include screen-size(small) {
			transform: translate(-50%, -50%);
			width: 85%;
		}

		input {
			background: white;
		}
	}
}

