$bullet-space-rich: 40px !default;
$rich-tail-white: 1em;

.pat-rich,
article.rich {
	-webkit-user-select: text;
	user-select: text;

	a {
		color: $colour-link;
		text-decoration: underline;

		&:before {
			text-decoration: none;
		}
	}

	.summary {
		font-size: 1.5em;
		line-height: 1.4;
		font-weight: thin;
		margin-bottom: 1em;
	}

	img {
		max-width: 100%;
	}

	h1 {
		font-size: 48px;
		line-height: 52px;
		font-weight: 400;
		margin-bottom: 25px;

		@media only screen and (max-width: 768px) {
			font-size: 28px;
			line-height: 36px;
		}
	}

	h2 {
		margin-bottom: 13px;
		font-size: 24px;
		line-height: 30px;
		// font-family: DaimlerCS-Demi;
		padding-top: 52px
	}

	h3 {
		margin-bottom: 13px;
		font-size: 24px;
		line-height: 30px;
		// font-family: DaimlerCS-Demi;
		margin-top: 35px;
		font-weight: normal;
	}

	h4 {
		font-weight: bold;
		font-size: 1em;
	}

	em,
	i {
		font-style: italic;
		color: black;
	}

	strong {
		font-weight: bold;
	}

	ul {
	    margin-left: 0;

		li {
			list-style-type: disc;
			list-style-position: outside;
			display: list-item;
			position: relative;
		    left: $bullet-space-rich;
		    padding-right: $bullet-space-rich;    
		}

	}

	ol {
		> li {
			margin-left: $bullet-space-rich;
			list-style: decimal;
		}

		p:last-child {
			margin-bottom: 0;
		}

		&.roman {
			list-style: upper-roman;
		}
	}

	ul,
	ol,
	table,
	p,
	dl {
		margin-bottom: $rich-tail-white;
	}

	figcaption {
		color: rgba(0,0,0,0.6);
		font-size: 0.8em;
	}

	.screenshot {
		overflow: hidden;
		position: relative;
		margin: 2 * $rich-tail-white 5%;

		&:before {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			content: ' ';
			z-index: 1;
		}

		.auto-scale-wrapper {
			margin-bottom: 5px;
		}
		
		object {
			user-select: none;
			-webkit-pointer-events: none;
			pointer-events: none;
			cursor: default;
			border: 1px solid silver;
			overflow: hidden;

			@include screen-size(large) {
				width: 1280px;
				height: 800px;
			}

			@include screen-size(medium) {
				width: 1024px;
				height: 768px;
			}

			@include screen-size(small) {
				width: 480px;
				height: 800px;
			}
		}

		img {
			border: 1px solid silver;
		}
	}

	ul.manual-checklist {
		li:before {
			@media print {
				font-family: fontello;
				content: $glyph-checkbox;
				margin-top: 0.1em;
			}
		}
	}

	ul.sample-list {
		li {
			padding-bottom: 0.5em;

			&:before {
				font-family: fontello;
				content: '#{$glyph-eyedropper}';
				font-weight: normal;
			}

			.byline {		
		      padding-bottom: 0.4em;
		      margin-bottom: 0;
		      font-size: 0.9em;

			}

		    .message {
		      margin: 0 0 7px 0;
		      font-size: 0.9em;
		      line-height: 1.4;
		      color: rgba(0,0,0,0.5);
		    }

			.sample-subject {
		        &:after {
		          font-family: fontello;
		          display: inline-block;
		          margin-left: 0.3em;
		          font-style: normal;
		          font-weight: normal;
		        }
			}

			&.sample-passed {
				.sample-subject {
					color: green;
			        
			        &:after {
			          content: "#{$glyph-ok-circled}";
			        }
				}
			}

			&.sample-problem {
				.sample-subject {
					color: red;
			        
			        &:after {
			          content: "#{$glyph-cancel-circled}";
			        }
				}
			}
		}
	}

}