dfn.iconified.icon-help-circle,
a.iconified.icon-help-circle,
.info-icon {
  color: $colour-link;
  cursor: default;
  top: 3px;
  @include iconified;
  line-height: 20px;
  height: 20px;
  width: 20px;
  font-style: normal;
  font-weight: normal;

  &:before {
    font-family: fontello;
    content: '#{$glyph-icon-info-circle}';
    text-align: center;
    width: 100%;
  }

  &:hover,
  &.tooltip-active-click {
    color: darken($colour-link, 10%);
  }
}